    job_description:
      "Job Description:\n\nAs a Java Back-end Application Developer, you will be focusing on the design, development, and maintenance of Java/JEE-based applications. You will apply your extensive experience in building enterprise applications, working with Core Java concepts, and collaborating in Git-based workflows. In this role, you will also work with relational databases, CI/CD pipeline automation, and testing frameworks, ensuring efficient software development and deployment practices.\n\nKey Responsibilities:\n Design, develop, and maintain Java/JEE-based enterprise applications\n Implement Core Java concepts including OOP, multi-threading, exception handling, and collections\n Collaborate with cross-functional teams to develop and deploy applications using Git Workflows\n Build and manage CI/CD pipelines, automating processes for efficient deployment\n Leverage relational database concepts, specifically Oracle, in back-end development\n Use Azure DevOps for project management and continuous integration\n Incorporate testing frameworks like Selenium, Cucumber, and JUnit in the development process\n Work with IBM SOA products including IIB/ACE, ODM, WAS, MQ, and ITX\n Develop front-end components using Node.js and Angular as required\n Troubleshoot and resolve issues in both front-end and back-end systems\n\nRequired Skills, Experiences, Education, and Competencies:\n Extensive experience in enterprise application development using Java EE technologies\n Proficiency in Core Java concepts such as OOP, multi-threading, exception handling, and generics\n Strong understanding of relational databases, specifically Oracle\n Experience with Git Workflows and CI/CD pipeline development\n Familiarity with Azure DevOps for project management and automation\n Expertise in testing frameworks including Selenium, Cucumber, Playwright, and JUnit\n Experience with IBM SOA products such as IIB/ACE, ODM, WAS, MQ, and ITX\n Knowledge of front-end development using Node.js and Angular\n Strong problem-solving and troubleshooting skills in software development\n\nThe hourly range for roles of this nature are $40.00 to $80.00/hr. Rates are heavily dependent on skills, experience, location, and industry.\n\ncyberThink is an Equal Opportunity Employer.",
