    job_description:
      "Our government client is looking for an experienced WordPress Developer on a hybrid 12+ months renewable contract role in North Chesterfield, VA.\n\nPosition  WordPress Developer\n\nJob Overview\n\nAs a WordPress Developer in the IT Division, you will be joining a highly dynamic and results oriented Department. You will be a key resource in providing enhancements and security for six WordPress applications while working closely with stakeholders. Your full stack development work will support agency's mission in highway safety, emergency messaging, and recruitment.\n\nYou Will Be Responsible For\n Working across the full stack in WordPress to build new features and services\n Developing solutions in WordPress that support positive, accessible user experiences in both desktop and mobile environments\n Debugging and security remediations for public facing applications\n Working with agile development methodologies, adhering to best practices, and pursuing continued learning opportunities\n\nHeres What You Need:\n Demonstrated expertise in WordPress application development\n Proficiency in PHP coding and debugging\n Proficiency in front-end technologies like HTML, CSS, and JavaScript\n Understanding of SQL\n Understanding of REST API\n Proven knowledge of back-end/infrastructure maintenance and optimization and UX/UI principles\n Proven knowledge of secure coding practices\n Ability to work as team member on web development projects end-to-end\n Outstanding communication skills\n Any combination of education and experience equivalent to a Bachelors degree in information sciences, computer science, or a related field; considerable software implementation experience\n\nRequired/Desired Experience:\n 5 Years of Demonstrated expertise in WordPress application development Required\n 5 Years of Proficiency in PHP coding and debugging Required\n 5 Years of Proficiency in front-end technologies like HTML, CSS, and JavaScript Required\n 5 Years of Understanding of SQL Required\n 5 Years of Understanding of REST API Required\n 5 Years of Ability to work as team member on web development projects end-to-end Required\n 5 Years of Proven knowledge of back-end/infrastructure maintenance and optimization and UX/UI principles Required\n 5 Years of Proven knowledge of secure coding practices Required\n 5 Years of Outstanding communication skills Required\n Any combination of education and experience equivalent to a Bachelors degree in information sciences, computer science, or a related field; Required\n\nAbout Vector\n\nVector Consulting, Inc., (Headquartered in Atlanta) is an IT Talent Acquisition Solutions firm committed to delivering results. Since our founding in 1990, we have been partnering with our customers, understanding their business, and developing solutions with a commitment to quality, reliability and value. Our continuing growth has been and continues to be built around successful relationships that are based on our organization's operating philosophy and commitment to ** People, Partnerships, Purpose and Performance - THE VECTOR WAY\n\nwww.vectorconsulting.com\n\nCelebrating 30 years of service.",
