    job_description:
      "Overview\n\nLMI is seeking a Full-Stack Software Developer to support a DOD Army Program.This position is a hybrid role with ability to work onsite 1-2 days a week in Ft. Liberty, NC.\n\nThe Full Stack Software Developer will provide support for application development of data and software to include analysis of mission needs, systems flow, and determination of data storage and usage requirements. You will work as part of a team with other contractors, government civilians, and military personnel in an agile environment.\n\nLMI is a consultancy dedicated to powering a future-ready, high-performing government, drawing from expertise in digital and analytic solutions, logistics, and management advisory services. We deliver integrated capabilities that incorporate emerging technologies and are tailored to customers' unique mission needs, backed by objective research and data analysis. Founded in 1961 to help the Department of Defense resolve complex logistics management challenges, LMI continues to enable growth and transformation, enhance operational readiness and resiliency, and ensure mission success for federal civilian and defense agencies.\n\nLMI has been named a 2022 and 2024 #TopWorkplace in the United States by Top Workplaces! We are honored to be recognized as a company that values a people-centered culture, and we are grateful to our employees for making this possible!\n\nThis position requires obtaining an active Secret clearance.\n\nResponsibilities\n\nThe Full Stack Software Developer duties may include:\n Executes software planning, requirements, risk management, design, development, architecture, modeling, estimation, configuration management, quality, security, and tests using software development methodologies, architectural structures, viewpoints, styles, design decisions, and frameworks across all lifecycle phases.\n Develops applications that integrate with PostgreSQL databases, enabling both read and write operations. Work with Senior developers and ensure best practices in software development.\n Develops data applications and ensures the scalability and reliability of software solutions.\n Integrates applications with PostgreSQL, utilizing tools like Streamlit, Dash and React to create robust, user-friendly data applications that enhance data accessibility and usability.\n Interact professionally with clients/internal teams; gather requirements and document the work in a ticketing system, designing and implementing new feature additions and improvements.\n Produce quality and maintainable code within a multidisciplinary team to provide tailored support of mission requirements.\n Utilize industry standard tools such as Gitlab for code version control, project management, product requirements, and user documentation.\n Automate retrieval of data from various sources via API and direct database queries for various personas using SQL and Python.\n Support DevSecOps activities for deploying applications and databases using Docker, Kubernetes, and Gitlab CI/CD pipelines.\n Support, maintain, and help document software functionality.\n Work directly with the customer to gather requirements and product feedback for future iterations.\n Support capabilities briefings for military personnel.\n Maintain coding, compliance, & security standards.\n Strong ability to analyze business requirements and create solutions. Ability to troubleshoot problems and provide timely resolutions.\n Strive for high quality and rapid output through the use of agile software development best practices including, but not limited to: performance tuning, testing, refactoring, usability, and automation.\n Clearly communicate with product owners to understand business concerns and demonstrate proposed solutions for acceptance.\n High energy, enthusiasm, tact, ability to effectively interact with Senior executives from Government and industry.\n Ability to create and foster a cooperative work environment.\n Self-directed, detail oriented in completing assigned tasks, able to adapt to changing work efforts and manage impact of shifting priorities.\n Availability for occasional travel.\n\nCore KSATs for DoD Cyber Workforce Framework (DCWF) roles:\n Knowledge of computer networking concepts and protocols, and network security methodologies.\n Knowledge of risk management processes (e.g., methods for assessing and mitigating risk).\n Knowledge of national and international laws, regulations, policies, and ethics as they relate to cybersecurity.\n Knowledge of cybersecurity principles.\n Knowledge of cyber threats and vulnerabilities.\n Knowledge of specific operational impacts of cybersecurity lapses.\n Knowledge of cloud computing service models Software as a Service (SaaS), Infrastructure as a Service (IaaS), and Platform as a Service (PaaS).\n Knowledge of cloud computing deployment models in private, public, and hybrid environment and the difference between on-premises and off-premises environments.\n\nQualifications\n\nMinimum\n DOD Cyber Workforce (DCWF) 8140 (621) Software Developer: A BS degree in Information Technology, Cybersecurity, Data Science, Information Systems, or Computer Science, from an ABET accredited or CAE designated institution fulfills the educational requirement for this WRC (conferred within 5 years or able to prove continuous relevant work experience) -OR- Personnel Certifications: Cloud+ or GICSP or SSCP or Security+ or GSEC. If none, must be willing to obtain in first 30-days.\n 5+ years of full-stack developer experience.\n Proficiency in Python.\n Experience with PostgreSQL.\n Hands on experience with web frameworks: Streamlit, Dash, and React.\n Experience in application development and database integration.\n Experience in interacting with APIs to provide integrated solutions.\n Experience in containerizing applications.\n Experience in CI/CD pipelines.\n Experience in Java.\n Experience working across the full architecture for web-based applications.\n Experience working with Agile software development with Scrum.\n Experience deploying COTS and GOTS systems to customer-controlled deployment environments.\n Experience writing automating test code and scripts.\n Demonstrated ability to use MS Office Suite to include Word, PowerPoint, and Excel.\n Superior communication skills, both oral and written.\n Ability to receive a Secret clearance immediatley after hire.\n\nPreferred\n Experience in POSIT ecosystem.\n Active Secret clearance\n Familiar with DOD Cybersecurity requirements and vulnerability management concepts.\n General: Linux, Docker, Kubernetes/K8S, APIs.\n Experience with Helm, YAML, Misc. scripting (bash/perl).\n Experience with implementing RBAC/ABAC methodologies to deployed software systems.\n Experience implementing microservice architectures.",
