    job_description:
      "Are you an experienced, passionate pioneer in technology who wants to work in a collaborative environment? As an experienced Java Full Stack Developer, you will have the ability to share new ideas and collaborate on projects as a consultant without the extensive demands of travel. If so, consider an opportunity with Deloitte under our Project Delivery Talent Model. Project Delivery Model (PDM) is a talent model that is tailored specifically for long-term, onsite client service delivery.\n\nWork you'll do/Responsibilities\n\n Design and Development: Lead the design and development of scalable and high-performance web applications using Java and related technologies.\n\n Front-End Development: Create responsive and user-friendly interfaces using HTML, CSS, JavaScript, and modern front-end frameworks such as Angular or React.\n\n Back-End Development: Develop robust and efficient server-side logic using Java, Spring Boot, and other relevant frameworks.\n\n Database Management: Design and manage databases, ensuring data integrity and optimizing performance using SQL and NoSQL databases.\n\n Code Reviews and Testing: Conduct code reviews, write unit tests, and perform integration testing to ensure code quality and reliability.\n\n Technical Documentation: Create and maintain comprehensive technical documentation for developed applications and systems.\n\n Quality Assurance: Participate in the development of automated tests and continuous integration pipelines to ensure the reliability of software releases.\n\n Application Development: Lead the development of new features and functionalities for web applications, ensuring they meet both functional and non-functional requirements.\n\n System Integration: Integrate third-party services and APIs to enhance application capabilities.\n\n Performance Optimization: Conduct performance tuning and optimization of applications to ensure high availability and scalability.\n\n Collaboration: Communicate regularly with Engagement Managers (Directors), project team members, and representatives from various functional and / or technical teams, including escalating any matters that require additional attention and consideration from engagement management. Independently and collaboratively lead client engagement workstreams focused on improvement, optimization, and transformation of processes including implementing leading practice workflows, addressing deficits in quality, and driving operational outcomes.\n\n Continuous Improvement: Stay updated with the latest industry trends and technologies, and continuously improve development processes and practices.\n\nThe Team\n\nOur Core Technology Operations (CTO) team offers differentiated operate services for our clients with solutions to help organizations scale and optimize critical business operations, drive speed to outcome, deliver business transformation, and build resilience in an uncertain future.\n\nOur operate services within CTO include:\n\n Foundry Services: Operate services providing flexible, recurring resource capacity for client initiatives, projects, tasks, and enhancement\n\n Managed Services: Operate services that provide ongoing maintenance, monitoring, and optimization for IT/Engineering applications & products\n\nQualifications\n\nRequired\n\n Minimum of 7 years of professional experience in full stack development, with a strong focus on Java, with a history of working on large and complex applications with high business and customer impact\n\n Front-End Skills: minimum of 7 years of professional experiencing using front-end technologies such as HTML, CSS, JavaScript, and experience with frameworks like Angular or React.\n\n Back-End Skills: minimum of 7 years of professional experience with Java, Spring Boot, and RESTful API development.\n\n Database Knowledge: minimum of 7 years of professional experience working with relational databases (e.g., MySQL, PostgreSQL) and experience with ORM frameworks like Hibernate.\n\n Version Control: minimum of 5 years of professional experience with version control systems, particularly Git.\n\n Minimum of 5 years of professional working experience leading client engagement workstreams focused on improvement, optimization, and transformation of processes including implementing leading practice workflows, addressing deficits in quality, and driving operational outcomes.\n\n Minimum of 5 years of professional working experience within Agile or Scrum environments.\n\n Minimum of 3 years Mentorship: Provide guidance and mentorship to junior developers, fostering a culture of continuous learning and improvement.\n\n Bachelor's degree, preferably in Computer Science, Information Technology, Computer Engineering, or related IT discipline; or equivalent experience\n\n Limited immigration sponsorship may be available.\n\n Ability to travel 10%, on average, based on the work you do and the clients and industries/sectors you serve.\n\n Must be able to start on or before 1/6/2025.\n\nPreferred\n\n Cloud Experience: Experience with cloud platforms such as AWS, Azure, or Google Cloud.\n\n DevOps: Familiarity with DevOps practices and tools, including CI/CD pipelines.\n\n Analytical/ Decision Making Responsibilities\n\n Analytical ability to manage multiple projects and prioritize tasks into manageable work products\n\n Can operate independently or with minimum supervision\n\n Excellent Written and Communication Skills\n\n Ability to deliver technical demonstrations\n\nThe wage range for this role takes into account the wide range of factors that are considered in making compensation decisions including but not limited to skill sets; experience and training; licensure and certifications; and other business and organizational needs. The disclosed range estimate has not been adjusted for the applicable geographic differential associated with the location at which the position may be filled. At Deloitte, it is not typical for an individual to be hired at or near the top of the range for their role and compensation decisions are dependent on the facts and circumstances of each case. A reasonable estimate of the current range is $97,875 to $145,000.\n\nYou may also be eligible to participate in a discretionary annual incentive program, subject to the rules governing the program, whereby an award, if any, depends on various factors, including, without limitation, individual and organizational performance.\n\nInformation for applicants with a need for accommodation: https://www2.deloitte.com/us/en/pages/careers/articles/join-deloitte-assistance-for-disabled-applicants.html\n\nAll qualified applicants will receive consideration for employment without regard to race, color, religion, sex, sexual orientation, gender identity, national origin, age, disability or protected veteran status, or any other legally protected basis, in accordance with applicable law.",
