    job_description:
      "Description\n\nThe National Security Sector within Leidos is seeking a C++ Software Developer with strong systems, software, cloud, and Agile experience to support a complex program to provide Agile development and operations and maintenance for critical systems on a mission-critical program supporting the Passenger Systems Program Directorate (PSPD) within Customs and Border Protection (CBP). PSPD supports the Department of Homeland Security (DHS) and CBP critical missions, specifically screening and processing travelers at the ports of entry (POEs) into the United States.\n\nThis position supports a program that handles all the travelers coming into the country via Airports, Seaports, Pedestrian, and Vehicle lanes. This position is focused on the devices attached to workstations and client applications associated with these areas including fingerprint readers and document scanners. Leidos is looking for a software engineer that can code in C++ and help troubleshoot Windows environment issues arising with the devices. Ideally, this individual will work independently and with other members of the Device team in research and evaluating new devices, develop code and wrappers layers for the devices, assist in the testing of the new devices and supporting deployment of the software and the devices to the workstations to ensure that all systems are working properly. This position also requires an individual to support testing with all of the client application teams as part of the testing phase of the new devices. In addition, this person must be available to support issues or concerns associated with the travelers if the need ever arrives. This includes participating in on-call rotation over evenings, holidays and weekends if it is needed.\n *This position REQUIRES the candidate to be in Ashburn, VA, once a week**\n\nMinimum Qualifications:\n Over 10 years of experience coding in C++, Java or other Windows type languages.\n BS/BA degree and 10 years of related experience or Masters with 8 years of related experience; OR 6 years of related experience in lieu of degree\n 3+ years experience developing cloud ready applications and/or experience migrating applications to the cloud.\n Experience in Troubleshooting problems\n Good Customer interaction and communication\n Good communication skills including documentation and ticket resolution\n On rare occasions, be willing to support the contract in evening, weekend, and holidays is ever needed\n Willing to travel to the DC area twice a month (occasional on-site visits to Ashburn and Lorton, occasional visit to testing location, and occasional visit to Dulles airport to support pilot projects)\n Can work with a team or independently\n Experience in Windows, Software Center, and looking into logs to determine cause of issues.\n Must be able to maintain and obtain a CBP Background Investigation prior to start\n Must be a US citizen\n\nPreferred Qualifications:\n Knowledge of fingerprint readers and document scanners\n Knowledge of different type of travel documents\n DHS BI clearance\n Develop test cases for testers to use during the testing phase\n Experience with Google Cloud\n\nOriginal Posting Date:\n2024-10-14\n\nWhile subject to change based on business needs, Leidos reasonably anticipates that this job requisition will remain open for at least 3 days with an anticipated close date of no earlier than 3 days after the original posting date as listed above.\n\nPay Range:\nPay Range $101,400.00 - $183,300.00\n\nThe Leidos pay range for this job level is a general guideline only and not a guarantee of compensation or salary. Additional factors considered in extending an offer include (but are not limited to) responsibilities of the job, education, experience, knowledge, skills, and abilities, as well as internal equity, alignment with market data, applicable bargaining agreement (if any), or other law.",
