    job_description:
      "Please submit a formal application through our site: Dev10 Apply Here We will not review any applications within this job posting. You must submit to our site. This job posting is for a Dev10 position that requires you to agree to relocate anywhere in the United States. Dev10 was created to provide an opportunity for those who want to break into the technology industry as a Software Developer but lack the skills or experience to get there on their own. We believe in the unlimited potential of people, and most importantly, the unlimited potential in YOU Did you realize too late in your degree program that you wished you had pursued technology? Do you wish you could pivot into tech from your current job or career path? Did a friend or family member suggest tech might be a field for you but are unsure where to start or how you would break into it? Here at Dev10, we are focused on your POTENTIAL, PASSION, and DESIRE TO LEARN regardless of your background or experience. Now hiring for our Software Developer role starting Monday, December 2, 2024 . The last day to apply is Friday, October 18, 2024 . If interested, please submit a formal application through our site: Dev10 Apply Here We will not review any applications within this job posting. You must submit to our site. What We Require Ability to relocate anywhere in the United States Bachelor's Degree in any discipline Ability to work in the United States without visa sponsorship now or at any point in the future What We Look For Passion for learning & interest in learning technology skills Logic & reasoning skills Communication skills Teamwork & collaboration skills What We Offer 3-4 months to focus on building the technical skills needed from the ground up (paid at $600/wk) Guidance from Senior Engineers who are passionate about sharing their expertise in an immersive training environment $2,500 relocation assistance 2 years of project experience with clients Hourly pay rate of $24.04 (equates to $50k annually) or $28.85 (equates to $60k annually) depending on assigned location Dev10 Mentor to help you navigate the corporate culture and support your success Benefits include health, vision, dental, 401k, paid time off Interested in learning more about whether a career in technology could be a good fit for you? Let's explore this career path together Please submit a formal application through our site: Dev10 Apply Here We will not review any applications within this job posting. You must submit to our site. For more information on Dev10, check out our website at http://www.dev-10.com/ About Dev10: Dev10 takes college graduates with the passion and aptitude to be entry-level software developers and provides hands-on training and client project experience all within a full-time paid position. No prior coding experience is required. Dev10 Associates work on exciting projects for Fortune 500 companies. To learn more about our roles within Dev10, please visit us at http://www.dev-10.com/ .",
