    job_description:
      "Farm Credit Mid-America is a financial services cooperative serving financial needs of farmers and rural residents in Arkansas, Indiana, Kentucky, Missouri, Ohio, and Tennessee. Our purpose is to secure the future of rural communities and agriculture. We are passionate about serving the needs of our rural communities and creating positive customer experiences.\n\nFarm Credit Mid-America is a great place to work! We have been named as a Best Places to Work in Kentucky for the past 12 years. At Farm Credit Mid-America we put our customers and team members at the heart of all we do.\n\nSCHEDULE: Full Time\n\nTRAVEL REQUIRED: 0% - 25%\n\nCOMPENSATION: $78,522.00 to $169,806.00 base salary plus potential for variable compensation. Base pay is determined by your skills, qualifications, experience, and location. Farm Credit Mid-America/Rural 1st may compensate outside of the salary range for bona fide reasons not related to membership in a protected class.\n\nAbout Our Opportunity\n\nOur Business Intelligence Developer provides analysis, design, testing, implementation and support of technologies to support the user facing presentation layer of the Data Warehouse (DWH), provides end-user support for Business Intelligence, Ad-hoc and Operational Reporting. Works with the DWH team and business users to identify, prioritize, and chaperone requirements through to user-accessible capabilities. Coordinates with departmental analysts and enterprise stakeholders to build and curate a collection of dashboards, reports, and interactive tools comprising a BI ecosystem.\n\nHow You Will Spend Your Time\n Provides technical and systems design expertise in the development and maintenance of Microsoft reporting products, including SQL Server Reporting Services (SSRS), Microstrategy, and other related data products.\n Coordinates with the DWH team and company stakeholders and Develop, test, implement, maintain and document reports and reporting processes.\n Maintains and curates a central repository of enterprise BI assets including dashboards, visualization and interactive reports. Monitor daily reports that identify data inconsistencies, determine the resolution and revise the incorrect operational data.\n Enables capabilities available in the BI technology such as drill-through functionality, KPI and scorecard reporting, advanced visualizations including mapping and geospatial analysis, predictive analytics, and other capabilities as they arise.\n Supports continuous process improvement efforts through the BI, DWH, and overall data infrastructure development lifecycle including development, testing, quality assurance, and deployment in line with FCMAs modified Agile practices.\n Monitors production reporting systems and use for performance, errors, and overall stability, and provides solutions to issues including performance tuning, emergency release and disaster recovery support.\n\nMinimum Education & Experience\n\nBachelors degree in computer science, business analytics or related field, and a minimum of two years relevant work experience building ad-hoc reports, deploying, and supporting business intelligence capabilities. Good project management skills, oral and written communication skills, and analytical skills necessary. Experience in the financial industry a plus.\n\nMental & Physical Requirements\n\nFarm Credit Mid-America provides a competitive total rewards package to our team members. This includes an award winning 401(k) Plan. Our Health and Financial wellness benefits provide safety, security and peace of mind for you and your family. Here is a highlight of our Total Rewards and Benefits.\n\nFarm Credit Mid-America is an equal opportunity employer, and all applicants will receive consideration for employments without regard to age, race, color, religion, sex, sexual orientation, gender identity, national origin, veteran status, disability, or any other category protected by law.\n\n#LI-HYBRID",
