    job_description:
      "Date Posted:\n2024-10-08\n\nCountry:\nUnited States of America\n\nLocation:\nTX217: 465 Independence Parkway 465 Independence Parkway , Plano, TX, 75075 USA\n\nPosition Role Type:\nOnsite\n\nAt Raytheon, the foundation of everything we do is rooted in our values and a higher calling  to help our nation and allies defend freedoms and deter aggression. We bring the strength of more than 100 years of experience and renowned engineering expertise to meet the needs of todays mission and stay ahead of tomorrows threat. Our team solves tough, meaningful problems that create a safer, more secure world.\n\nOur Airborne & Space Surveillance RF Sensor Department Advanced RF Systems Section has an immediate career growth opportunity for a Radar Algorithm Mode Developer. The Radar Algorithm Mode Developer will join our Airborne Spectrum Dominance (ASD) Emerging Systems product line to provide radar algorithm/signal processing mode development to support distributed aperture radar programs. Mode development includes working on multi-function radar capabilities such as Airborne Moving Target Indicator (AMTI), Ground Moving Target Indicator (GMTI), maritime surveillance, sense and avoid, weather detection, terrain detection/avoidance, Synthetic Aperture Radar (SAR), and communications.\n\nWhat You Will Do:\n Provide technical direction leading radar and communications mode design, including software specification definition for implementation of mode functionality\n Lead the design and development of radar and communication waveforms as well as conduct, support, and direct signal processing and waveform analyses\n Develop mathematical algorithms for radar signal processing, make performance predictions and analyze data received from the radar to evaluate algorithm performance, make technical presentations, and participate in technical product reviews\n Interface with engineering teams (e.g., Software, Hardware, Firmware) and program management to ensure consistent application of engineering best practices\n Work with both customers and internal/external engineering disciplines and stakeholders\n\nQualifications You Must Have:\n Typically requires bachelors degree in Science, Technology, Engineering or Mathematics (STEM) and eight (8) years OR an advanced degree with six (6) years in radar systems engineering and algorithm development to include radar systems, software engineering, radar signal processing techniques, mathematical / radar mode algorithm development, analytic techniques, and digital simulation models (DSM)\n MATLAB and C++ (coding experience is required to implement algorithms into the programs digital system simulation model)\n Ability to prepare and create technical presentations and brief internally and externally at a customer level\n Active and transferable U.S. government issued Secret security clearance is required prior to start date. U.S. citizenship is required, as only U.S. citizens are eligible for a security clearance.\n\nQualifications We Prefer:\n Model Based Systems Engineering (MBSE) tools to include Cameo EA\n Mathcad, Python, or related languages and tools\n Experience with terrain detection / terrain avoidance and/or TF Navigation algorithms\n Radar mode development skills including maritime surveillance, MTI, air-to-air, sense and avoid, weather detection, and synthetic aperture radar.\n Digital communications\n Strapdown inertial navigation and transfer alignment\n Target discriminators and feature extraction / exploitation\n Machine-learning\n Radar clutter modeling\n\nWhat We Offer\n Our values drive our actions, behaviors, and performance with a vision for a safer, more connected world. At RTX we value: Trust, Respect, Accountability, Collaboration, and Innovation.\n Relocation Eligible\n\nLearn More & Apply Now!\n RTX solves some of the toughest challenges in aerospace and defense. That requires expansive thinking and bold innovation  and that, in turn, requires a culture that is diverse, equitable and inclusive.\n We embrace individuality and diversity of thought to fuel opportunity for our employees, our customers, and our communities. We work toward progress, knowing that a more inclusive world is critical to our mission. Not just in this moment, but always.\n\nThe salary range for this role is 96,000 USD - 200,000 USD. The salary range provided is a good faith estimate representative of all experience levels. RTX considers several factors when extending an offer, including but not limited to, the role, function and associated responsibilities, a candidates work experience, location, education/training, and key skills.\n\nHired applicants may be eligible for benefits, including but not limited to, medical, dental, vision, life insurance, short-term disability, long-term disability, 401(k) match, flexible spending accounts, flexible work schedules, employee assistance program, Employee Scholar Program, parental leave, paid time off, and holidays. Specific benefits are dependent upon the specific business unit as well as whether or not the position is covered by a collective-bargaining agreement.\n\nHired applicants may be eligible for annual short-term and/or long-term incentive compensation programs depending on the level of the position and whether or not it is covered by a collective-bargaining agreement. Payments under these annual programs are not guaranteed and are dependent upon a variety of factors including, but not limited to, individual performance, business unit performance, and/or the companys performance.\n\nThis role is a U.S.-based role. If the successful candidate resides in a U.S. territory, the appropriate pay structure and benefits will apply.\n\nRTX anticipates the application window closing approximately 40 days from the date the notice was posted. However, factors such as candidate flow and business necessity may require RTX to shorten or extend the application window.\n\nRTX is An Equal Opportunity/Affirmative Action Employer. All qualified applicants will receive consideration for employment without regard to race, color, religion, sex, sexual orientation, gender identity, national origin, disability or veteran status, age or any other federally protected class.\n\nPrivacy Policy and Terms:\n\nClick on this link to read the Policy and Terms",
