    job_description:
      "The position is described below. If you want to apply, click the Apply Now button at the top or bottom of this page. After you click Apply Now and complete your application, you'll be invited to create a profile, which will let you see your application status and any communications. If you already have a profile with us, you can log in to check status.\n\nNeed Help?\n\nIf you have a disability and need assistance with the application, you can request a reasonable accommodation. Send an email to Accessibility (accommodation requests only; other inquiries won't receive a response).\n\nRegular or Temporary:\nRegular\n\nLanguage Fluency: English (Required)\n\nWork Shift:\n1st shift (United States of America)\n\nPlease review the following job description:\n\nDeliver highly complex solutions with significant system linkages, dependencies, associated risk. Perform development efforts such as analysis, design, coding, collaborating and supporting testing.\n\n75% Coding\n25% Working with Agile teammates\n\nESSENTIAL DUTIES AND RESPONSIBILITIES\nFollowing is a summary of the essential functions for this job. Other duties may be performed, both major and minor, which are not mentioned below. Specific activities may change from time to time.\n Participate in testing, implementation, maintenance, and escalated support of Truist's most complex solutions.\n Development includes but is not limited to customized coding, software integration, analysis, configuring solutions, or use tools pertinent to the area, project, or system to configure or generate a solution.\n Ensure adherence to company and industry standards.\n Devise or modify procedures to solve complex problems considering computer equipment capacity and limitations, operating time, and form of desired results.\n Interpret internal/external business challenges and recommends best practices.\n Use sophisticated analytical thought to exercise judgment and identify innovative solutions.\n In an Agile environment: Act as an Agile champion. Responsible for delivering high quality working software and automating manual/reusable tasks working directly, and consulting with, the business from the beginning of the design work. Leverage continuous engineering practices to deliver business value regarding effectiveness of the design.\nActively participate in refining user stories. Responsible for development and developing unit testing and supporting integration and functional testing. Responsible for providing warranty support and providing knowledge transfer to production support. Develop code in accordance with the acceptance criteria established by the Product Owner.\n\nQUALIFICATIONS\nRequired Qualifications\nThe requirements listed below are representative of the knowledge, skill and/or ability required. Reasonable accommodations may be made to enable individuals with disabilities to perform the essential functions.\n Bachelors Degree and 4 to 7 years of experience or equivalent education and software engineering training or experience\n In-depth knowledge in information systems and ability to identify, apply, and implement IT best practices\n Understanding of key business processes and competitive strategies related to the IT function\n Ability to plan and manage projects and solve complex problems by applying best practices\n Ability to provide direction and mentor less experienced teammates. Ability to interpret and convey complex, difficult, or sensitive information\n\nPreferred Qualifications:\n 5+ years of experience developing responsive web-based applications in multiple UI technologies using Angular.\n Experience in developing UI and Prototype using HTML5, CSS, SCC Angular 12+, Javascript, Jquery, Bootstrap, NodeJS and other NPM packages.\n Experience in integrating REST and SOAP based services.\n Experience with Performance Tuning, Scaling, Security, Resiliency Best Practices.\n Experience with one or more Test Frameworks and Capabilities & Tools: Jasmine, Selenium, Cucumber.\n Experience in building Cloud native applications.\n Experience with one or more CI/CD implementation, configuration management and orchestration tools like Jenkins, GitHub, Maven, TerraForm or Harness\n Experience in debugging distributed systems with high data loads.\n Have working experience with Scrum/Agile development methodologies specifically in SAFe\n Knowledge and understanding of the needs of the business units and how these needs can be addressed through technology.\n Demonstrated ability to work in a fast pace variable environment.\n Banking or financial services experience.\n\nGeneral Description of Available Benefits for Eligible Employees of Truist Financial Corporation: All regular teammates (not temporary or contingent workers) working 20 hours or more per week are eligible for benefits, though eligibility for specific benefits may be determined by the division of Truist offering the position. Truist offers medical, dental, vision, life insurance, disability, accidental death and dismemberment, tax-preferred savings accounts, and a 401k plan to teammates. Teammates also receive no less than 10 days of vacation (prorated based on date of hire and by full-time or part-time status) during their first year of employment, along with 10 sick days (also prorated), and paid holidays. For more details on Truists generous benefit plans, please visit our Benefits site. Depending on the position and division, this job may also be eligible for Truists defined benefit pension plan, restricted stock units, and/or a deferred compensation plan. As you advance through the hiring process, you will also learn more about the specific benefits available for any non-temporary position for which you apply, based on full-time or part-time status, position, and division of work.\n\nTruist supports a diverse workforce and is an Equal Opportunity Employer that does not discriminate against individuals on the basis of race, gender, color, religion, citizenship or national origin, age, sexual orientation, gender identity, disability, veteran status or other classification protected by law. Truist is a Drug Free Workplace.\n\nEEO is the Law Pay Transparency Nondiscrimination Provision E-Verify",
