    job_description:
      "At General Dynamics Information Technology (GDIT) you'll be part of a team that makes a true impact by building cutting-edge mission-critical applications that help the end user accomplish their mission and keep people safe. The work we do is important. The challenges we face are career-defining. The opportunity we can offer is one-of-a-kind.\n\nOur work depends on a Front End React Developer joining our team to support a GDIT program that we are the prime on and is located at Annapolis Junction, MD. As a Front End React Developer, you will have the opportunity to design and enhance various mission critical UIs like geospatial awareness tools using ReactJS due to rapid growth in data processing requirements.\n\nThe Front End React Developers will work with cutting-edge technologies including:\n\nTechnologies: React 18, Redux, Vite, JavaScript, TypeScript, Microservices, AWS, Docker, Kafka, Kubernetes, Cassandra\n\nTools: Yarn, Node, GitLab, VSCode\n\nKEY RESPONSIBILITIES:\n Design, develop and support production user facing applications in support of mission objectives\n Provide ongoing development, maintenance, support, and software enhancements in existing systems and platforms\n Troubleshoot complex problems and provide customer support for software systems and application issues\n Provide recommendations for continuous improvements\n Work alongside other engineers on the team to sustain and advance our organizations capabilities\n\nREQUIREMENTS AND QUALIFICATIONS:\n The position requires an active TS/SCI with Polygraph\n Bachelors Degree in Computer Science, Computer Engineering or a related technical discipline, plus 5 years of application design and development experience.\n Minimum of 5 years experience with JavaScript/Front End Development\n Minimum of 2 years of experience with React\n Minimum of 5 years of experience with Linux\n Minimum of 5 years of direct experience with the development of software components within the Intelligence Community\n Minimum of 1 year direct experience with Microservices\n\nPREFERRED SKILLSETS:\n Familiarity with Redux\n\nWHAT GDIT CAN OFFER YOU\n 401K with company match\n Rewards program for high-performing employees\n Comprehensive health and wellness packages\n Professional growth opportunities including paid education and certifications\n Rest and recharge with paid vacation and holidays\n Internal mobility team dedicated to helping you own your career\n Diverse, highly collaborative teams\n Challenging work that makes a real impact on the world around you\n Cutting-edge technology you can learn from\n Flexible work schedule\n Work Life balance\n\n#SWDevPolyMD\n\n#EnhancedTechPoly2025",
