    job_description:
      "Are You a Senior PHP Developer Ready for a Game-Changing Career in Cybersecurity?\n\nHey there! Were on a mission to protect a million people by revolutionizing cybersecurityand we need your help. As a small, dynamic team, every line of code you write and every idea you bring to the table matters a lot. If youre looking for a role where you can directly influence the outcome and work shoulder-to-shoulder with our leadership (including our Founder/CEO), you've just found your next big adventure.\n\nWhat We Offer:\n\nDirect Impact: Your voice will be heard. As part of our team, your ideas will shape our future, and you'll see the results firsthand.\n\nDaily Challenges: Cybersecurity is ever-evolving, and so are the challenges. Expect a new puzzle every day, keeping you on your toes and continuously expanding your skills.\n\nRemote Flexibility: Join our team from anywhere in the U.S. Whether you're in Nashville or nestled in the mountains of Utah, our 100% remote setup supports your best work from your favorite spot.\n\nBenefits and Beyond: We not only offer a competitive salary and 401k with match, but also health, dental, and vision insurance reimbursements, and a flexible vacation policy to support your work-life balance.\n\nWhat Youll Do:\n\n- Design, code, test, and manage innovative applications.\n\n- Collaborate with a small but mighty engineering team to develop best-in-class cybersecurity solutions.\n\n- Utilize minimal third-party libraries, crafting clean, efficient code from scratch.\n\n- Stay agile, with quick adaptations to new technologies and methodologies as needed.\n\nWho You Are:\n\n- You have a B.S. in Computer Science (or comparable experience) with a solid portfolio.\n\n- You bring 6+ years of experience with PHP, preferably with Laravel, and a deep understanding of MS SQL Server and MySQL.\n\n- Your skills in JavaScript, CSS, HTML, and other front-end languages are top-notch.\n\n- Youre a self-starter with an entrepreneurial spirit, ready to take on projects with minimal supervision but maximum impact.\n\nInterested? If you're ready to make a significant impact in a role where your contributions are immediately felt, lets chat. Join us to make cybersecurity 31 times better for businesses around the globe.\n\nThis is a US-based, full-time position. Ready to change the world from your home office? Apply now!\n\nJob Type: Full-time\n\nPay: $75,000.00 - $125,000.00 per year\n\nBenefits:\n 401(k)\n 401(k) matching\n Dental insurance\n Flexible schedule\n Flexible spending account\n Health insurance\n Paid time off\n Retirement plan\n Vision insurance\n\nCompensation Package:\n Bonus opportunities\n\nApplication Question(s):\n Describe one development project you made better. What was one thing you did to make it better?\n\nEducation:\n Bachelor's (Required)\n\nExperience:\n PHP: 6 years (Required)\n Laraval: 2 years (Required)\n\nWork Location: Remote",
