    job_description:
      "Microsoft Software Developer - Indianapolis, Indiana\n Designing, coding, testing, debugging, maintaining and documenting complex software applications are the primary responsibilities of a Fusion Alliance Microsoft Software Developer, Consultant. The developer also serves to support our clients critical business functions through development, architectural design, implementation and integration work. Our software developers can expect to provide expertise to multiple projects using various technologies.\nWhat we look for in a Fusion Alliance Microsoft Software Developer:\n Experience building complex, scalable, and high-performance systems that have been successfully delivered to customers\n Understands the need for continuous learning in the development space and can learn quickly\n Implements a solution architecture with custom designs combined with reusable components\n Ability to identify real-world user problems and build technology solutions to solve them\n Skilled in prototyping, transaction processing, web-based interactive applications and relational databases\n In-depth development experience with Microsoft .NET and MS products like SQL Server, SharePoint, Azure\n Proficient in every area of the project lifecycle (from new development to defect remediation)\n Experience using industry standard tools to manage the development workflow\n Communication skills to work successfully in a team environment\n Develops applications using service-oriented approaches/frameworks to integrate with business partners\nTechnical Skills:\n Familiarity with the .NET framework\n Strong understanding of object-oriented programming\n Skill for writing reusable C# libraries\n Familiar with various design and architectural patterns\n Knowledge of concurrency patterns in C#\n Familiarity with Microsoft SQL Server\n Experience with popular web application frameworks, such as Nancy\n Familiarity with Windows Presentation Framework (WPF) and/or Windows Communication Framework (WCF)\n Knack for writing clean, readable code\n Understanding fundamental design principles behind a scalable application\n Creating database schemas that represent and support business processes\n Basic understanding of Common Language Runtime (CLR), its limitations, weaknesses, and workarounds\n Implementing automated testing platforms and unit tests\n Proficient understanding of code versioning tools such as Git, Subversion (SVN) and Mercurial (Hg)\n Familiarity with continuous integration\nDesired Qualities:\n Qualified, educated individuals who have 5+ years of relevant professional and technological experience coupled with a perpetual hunger to learn and grow\n People who thrive in a socially engaged culture with a strong value system, employee initiatives, community activities and charity efforts\n Productive, goal-oriented candidates who want to make a real impact on their organization, their customers and the world\n People who are not just technically savvy, but are professionally and socially perceptive and flexible enough to make technical information easily understood by anyone in an organization\n\nFusion Alliance is an Equal Opportunity Employer. All qualified applicants will receive consideration for employment without regard to race, color, religion, sex, national origin, age, disability or veteran status.\n\nApply Today!",
