    job_description:
      "Nintendo of America Inc.\n\nAbout Nintendo: Nintendo Co., Ltd., headquartered in Kyoto, Japan, has created franchises that have become household names worldwide, including Mario, Donkey Kong, The Legend of Zelda, Metroid, Pokmon, Animal Crossing, Pikmin and Splatoon, through its integrated hardware and software products. Nintendo aims to deliver unique, intuitive entertainment experiences for everyone, manufacturing and marketing video game devices such as the Nintendo Switch family of systems, developing and operating applications for smart devices, and collaborating with partners on a range of other entertainment initiatives like visual content and theme parks. Nintendo has sold more than 5.8 billion video games and over 850 million hardware units globally. From the launch of the Nintendo Entertainment System more than 30 years ago, through today and into the future, Nintendos continuing mission is to create unique entertainment that puts smiles on the faces of people all over the world. A wholly owned subsidiary, Nintendo of America Inc., based in Redmond, Wash., serves as headquarters for Nintendos operations in the Americas. For more information about Nintendo, please visit the companys website at https://www.nintendo.com/.\n * This is a CONTRACT position\n\nDESCRIPTION OF DUTIES:\n\nParticipates in all phases of the development life cycle, focusing on coding, testing, and debugging\nWorks on the maintenance or enhancement of existing applications\nResearches, evaluates and analyzes technical and design requirements\nParticipates in cross functional discussions and effectively communicates information, both written and verbally to members of the project team\nMaintains up-to-date knowledge of related tools, trends and developments\nWorks with team members to establish consistent development and documentation guidelines\nDemonstrates good judgment in selecting methods and techniques for obtaining solutions\nDocuments all technology changes through appropriate change control\nProvides support and troubleshooting of prior developed technology solutions\nParticipate in design brainstorming sessions, technical design reviews, formal code reviews, site QA, and functional demos\nEnsures work is completed in such a way to comply with established J-SOX and other internal controls",
