    job_description:
      "Software DeveloperThe Opportunity: As a developer, you know that a good site or system needs the right combination of clean code, APIs, analytics, and infrastructure to develop a user-focused solution. Were looking for an experienced developer with the software engineering skills it takes to identify potential risks, contribute to solution development, and create efficient and effective systems for our clients. As a developer at Booz Allen, youll use the latest architectural approaches and open-source frameworks and tools to deliver solutions. Using your software engineering experience, youll work with the development team to create custom tools, systems, and sites with consistent performance and scalability. In this role, youll make a mission-forward impact as you further your skillset and career. Work with us as we shape systems for the better. Join us. The world cant wait. You Have: 10+ years of experience designing, developing, and implementing features for a Java application Experience with Core Java/J2EE, Angular or Spring FrameworkExperience coordinating with customers and stakeholders on requirements, negotiate on API design and specifications utilizing OpenAPI, and implement changesExperience with web servicesKnowledge of Docker and KubernetesKnowledge of software development life cycle methodologies, including Agile and ScrumSecret clearanceBachelor's degreeNice If You Have: 5+ years of experience with large database implementations and analysisExperience with WebLogic and JBOSS, web services, such as SOAP or REST, myBATIS, Maven, ANT, databases, MySQL, NoSQL, or OracleExperience with Angular or other JavaScript frameworksExperience in Agile or ScrumExperience with Department of Defense (DoD) policy or procedure analysisExperience with the Risk Management FrameworkAbility to multitask and prioritize multiple projects, deadlines, and responsibilitiesAbility to distill complex problems through the application of design patternsPossession of excellent written and verbal communication skillsCompTIA Security+ CertificationClearance: Applicants selected will be subject to a security investigation and may need to meet eligibility requirements for access to classified information; Secret clearance is required.CompensationAt Booz Allen, we celebrate your contributions, provide you with opportunities and choices, and support your total well-being. Our offerings include health, life, disability, financial, and retirement benefits, as well as paid leave, professional development, tuition assistance, work-life programs, and dependent care. Our recognition awards program acknowledges employees for exceptional performance and superior demonstration of our values. Full-time and part-time employees working at least 20 hours a week on a regular basis are eligible to participate in Booz Allens benefit programs. Individuals that do not meet the threshold are only eligible for select offerings, not inclusive of health benefits. We encourage you to learn more about our total benefits by visiting the Resource page on our Careers site and reviewing Our Employee Benefits page.Salary at Booz Allen is determined by various factors, including but not limited to location, the individuals particular combination of education, knowledge, skills, competencies, and experience, as well as contract-specific affordability and organizational requirements. The projected compensation range for this position is $84,600.00 to $193,000.00 (annualized USD). The estimate displayed represents the typical salary range for this position and is just one component of Booz Allens total compensation package for employees. This posting will close within 90 days from the Posting Date.Work ModelOur people-first culture prioritizes the benefits of flexibility and collaboration, whether that happens in person or remotely.If this position is listed as remote or hybrid, youll periodically work from a Booz Allen or client site facility.If this position is listed as onsite, youll work with colleagues and clients in person, as needed for the specific role.EEO CommitmentWere an equal employment opportunity/affirmative action employer that empowers our people to fearlessly drive change  no matter their race, color, ethnicity, religion, sex (including pregnancy, childbirth, lactation, or related medical conditions), national origin, ancestry, age, marital status, sexual orientation, gender identity and expression, disability, veteran status, military or uniformed service member status, genetic information, or any other status protected by applicable federal, state, local, or international law.",
