    job_description:
      "Software Developer\n\nHybrid\n\nAbout the job\n Location: Jacksonville, FL\n Duration: 6-months\n Pay rate: W2 Salary/ W2 Hourly\n Job ID: 1329-1\n\nNo C2C. No subcontracting. Must be able to work on W2 with DataSoft Technologies.\n\nOverview\n\nDataSoft Technologies, Inc. is seeking a Software Developer to join our client in Jacksonville, FL. The individual will be responsible for utilizing established procedures, guidelines, and industry knowledge and experience to analyze current business information systems, applications, and tools in order to identify and implement moderately complex solutions that meet organizational needs and requirements, under general supervision.\n\nResponsibilities:\n Performs need, trend, feasibility and performance assessments related to business information systems and applications to ensure business requirements are fulfilled, risks are mitigated, and value is optimized.\n Designs, codes, tests and analyzes existing and new software.\n Makes and implements recommendations that improve system design and/or streamline performance and productivity based on analysis.\n Develops and documents best practices based on management and end-user feedback. Coordinates the installation of new software systems and provides ongoing technical support for moderately complex systems, applications and tools.\n Conducts code and configuration audits in order to assess accuracy and performance. Creates moderately complex technical documentation, such as software user guides, that enhance the operation, maintenance and development of systems.\n Anticipates internal/external business challenges and recommends and implements system value opportunities, including process optimization, policies and procedures, and end-user experience improvements.\n May indirectly lead other, less experienced, software developers in the implementation of small scale projects or components of larger projects.\n Establishes and maintains partnerships internal/external of the organization to maintain a unified approach to software development and assist in facilitating the resolution of software and business information system issues.\n\nQualifications:\n 8+ years of experience as a full stack developer with deep knowledge in Angular 16+, .Net Core 5 +, MS SQL, PostgreSQL.\n Design, develop and support the in-house applications.\n Experience in design and building Rest and Soap APIs\n Experience in designing and maintaining Databases.\n Experience in integration with external systems.\n Experience in GIT and adhere to DevOps CI/CD standards through existing pipelines.\n Adhere to scrum methodology through ceremonies\n\nAbout our Company\n\nDataSoft Technologies is a highly recognized provider of professional IT Consulting services in the US. Founded in 1994, DataSoft Technologies, Inc. provides staff augmentation services for Information Technology and Automotive Services. Our team member benefits include:\n Paid Holidays/Paid Time Off (PTO)\n Medical/Dental Insurance\n Vision Insurance\n Short Term/Long Term Disability\n Life Insurance\n 401 (K)",
