    job_description:
      "JOB LEVEL\nM40\n\nEMPLOYEE ROLE\nPeople Manager\n\nJob Summary:\n\nAs the Sr Engineering Manager for Developer Experience at our flagship product Photoshop, you will lead a team of senior software engineers dedicated to improving productivity and effectiveness of our core Photoshop developers. You will work closely with engineering, product, and design teams to create tools, libraries, and best practices that enhance the developer experience. Your goal is to ensure that our developers have the best possible environment to create, build, and innovate.\n\nKey Responsibilities:\n Team Leadership:\n Lead, mentor, and grow a high-performing team of engineers focused on developer experience.\n Foster a collaborative and inclusive team culture.\n Provide regular feedback, conduct performance reviews, and support professional development.\n Developer Tools & Infrastructure:\n Oversee the development and maintenance of tools, libraries, and frameworks that improve developer productivity.\n Champion cross-functional efforts to identify and resolve issues that adversely affect developer efficiency\n Process Improvement:\n Drive continuous improvement in the developer workflow, including build, test, and deployment processes.\n Metrics based decision making and promote best practices for software development workflow like code quality, CI/CD pipelines etc.\n Collaboration & Communication:\n Work closely with product and engineering teams to understand their needs and challenges.\n Advocate for the developer perspective in cross-functional discussions and planning.\n Ensure clear and consistent communication of goals, priorities, and progress.\n Innovation & Strategy:\n Stay abreast of industry trends and emerging technologies.\n Develop and execute a strategic vision for enhancing the developer experience at Photoshop.\n Drive innovation by fostering an atmosphere of experimenting with new tools and approaches to improve development efficiency.\n\nQualifications:\n 5+ years of experience as an engineering manager or similar leadership role.\n 8+ years of experience in software development with a deep understanding of developer tools and processes.\n Experience with agile methodologies and software development lifecycle.\n Bachelor's degree required\n Technical Skills:\n Proficiency in programming languages such as Python, C++\n Familiarity with modern development tools and environments (e.g., Git, Docker, Kubernetes, Jenkins, Actions, CI/CD tools).\n Understanding of cloud platforms and services (e.g., AWS, Azure, GCP).\n Leadership Skills:\n Excellent leadership and people management skills.\n Ability to inspire, motivate, and develop team members.\n Strong communication and interpersonal skills.\n Problem-Solving:\n Strong analytical and problem-solving abilities.\n Ability to navigate complex technical and organizational challenges.\n\nPreferred Qualifications:\n Background in DevX practices and principles.\n Knowledge of security best practices in software development.\n\nOur compensation reflects the cost of labor across several U.S. geographic markets, and we pay differently based on those defined markets. The U.S. pay range for this positionis $146,300 -- $281,100 annually. Paywithin this range varies by work locationand may also depend on job-related knowledge, skills,and experience. Your recruiter can share more about the specific salary range for the job location during the hiring process.\n\nAt Adobe, for sales roles starting salaries are expressed as total target compensation (TTC = base + commission), and short-term incentives are in the form of sales commission plans. Non-sales roles starting salaries are expressed as base salary and short-term incentives are in the form of the Annual Incentive Plan (AIP).\n\nIn addition, certain roles may be eligible for long-term incentives in the form of a new hire equity award.\n\nAdobe will consider qualified applicants with arrest or conviction records for employment in accordance with state and local laws and fair chance ordinances.\n\nInternal Opportunities\n\nCreativity, curiosity, and constant learning are celebrated aspects of your career growth journey. Were glad that youre pursuing a new opportunity at Adobe!\n\nPut your best foot forward:\n\n1. Update your Resume/CV and Workday profile  dont forget to include your uniquely Adobe experiences and volunteer work.\n\n2. Visit the Internal Mobility page on Inside Adobe to learn more about the process and set up a job alert for roles youre interested in.\n\n3. Check out these tips to help you prep for interviews.\n\n4. If you are applying for a role outside of your current country, ensure you review the International Resources for Relocating Employees on Inside Adobe, including the impacts to your Benefits, AIP, Equity & Payroll.\n\nOnce you apply for a role via Workday, the Talent Team will reach out to you within 2 weeks. If you move into the official interview process with the hiring team, make sure you inform your manager so they can champion your career growth.\n\nAt Adobe, you will be immersed in an exceptional work environment that is recognized around the world. You will also be surrounded by colleagues who are committed to helping each other grow through our unique Check-In approach where ongoing feedback flows freely. If youre looking to make an impact, Adobe's the place for you. Discover what our employees are saying about their career experiences on the Adobe Life blog and explore the meaningful benefits we offer.\n\nAdobe is an equal opportunity and affirmative action employer. We welcome and encourage diversity in the workplace regardless of gender, race or color, ethnicity or national origin, age, disability, religion, sexual orientation, gender identity or expression, veteran status, or any other characteristics protected by law.\n\nIf you have a disability or special need that requires accommodation to navigate our internal careers site or to complete the application process, please contact accommodations@adobe.com.",
