    job_description:
      "Were seeking a Senior Developer Advocate to play a pivotal role in enabling our internal engineering teams to harness the full power of Gen AI and data for transformative applications across our product suite.\n\nABOUT THE TEAM:\n\nAt Intuit, our Developer Advocacy team is more than just a group of technologists; we are the champions of developer success, driving the future of cloud native open source innovation across Intuits development platform. We are dedicated to building a vibrant, inclusive community where developers are empowered to explore, innovate, and thrive.\n\nOur mission is to bridge the gap between Intuits cutting-edge cloud technologies and the developers who bring them to life, turning possibilities into realities. We believe that by supporting and uplifting developers, both inside and outside of Intuit, we can shape not just the evolution of our cloud native technologies, but the entire landscape of container-based developer platforms.\n\nABOUT THE ROLE:\n\nAs the Senior Developer Advocate, Data & AI, you will be the champion of Intuits AI and data platforms within the organization, enabling internal teams through education, support, and technical leadership in AI and data tools. Your role will involve working closely with product managers, data engineers, software developers, and other internal stakeholders to ensure seamless adoption and integration of Intuits AI/ML technologies. You will facilitate cross-team collaboration, provide guidance on AI best practices, and advocate for internal needs to product and engineering teams to drive innovation and efficiency.\n\nYou will advocate for new models and capabilities such as qodo (formerly CodiumAI), GPT-4, SageMaker, Vertex, LangChain, Large Language Models (LLMs), Prompt Engineering, DialogFlow, Python and more by building high-quality sample apps and demos, creating compelling technical content, suggesting code snippets to shape our dev docs, and developing new tools for our engineers to use as they build on top of Intuits Development Platform. The role requires a blend of technical expertise, community engagement, and content creation to enhance the capabilities of developers working with cloud-based AI services, machine learning models, natural language processing, and data analytics.\n\nThis role is ideal for someone passionate about AI, with a deep understanding of machine learning frameworks and data platforms, who can both code and communicate effectively.\nResponsibilities\n Content Creation & Technical Writing: Develop technical resources including blog posts, tutorials, videos, and sample code to simplify the adoption of Intuits AI tools. Lead the creation of internal knowledge bases, including FAQs, tutorials, and technical papers to accelerate learning and solution implementation.\n Internal Enablement & Education: Deliver internal workshops, technical training sessions, and onboarding materials to support teams in deploying AI/ML solutions. Be the go-to expert on AI and data best practices, helping teams solve complex technical challenges.\n Developer Engagement & Community Building: Build and nurture relationships with developers, data scientists, and engineers by contributing to forums, leading webinars, hosting workshops, and engaging on social media platforms. Support and grow open source initiatives and community driven projects around Intuits AI and data offerings.\n Thought Leadership in AI & Data: Stay on the cutting edge of AI and machine learning technologies, and translate complex technical concepts into easily digestible content for the developer community. Present at internal town halls, leadership meetings, and team all-hands to highlight how AI and data tools can drive impact across different parts of the business.",
