    job_description:
      "V2Soft (www.v2soft.com) is a global company, headquartered out of Bloomfield Hills, Michigan, with locations in Mexico, Italy, India, China, and Germany. At V2Soft, our mission is to provide high-performance technology solutions to solve real business problems. We become our customer's true partners, enabling both parties to enjoy success. We are committed to promoting diversity in the workplace and believe it has a positive effect on our company and the customers we serve.\n\nPosition General Summary\n\nThe Software Developer designs, builds and supports learning systems application development and 3rd party product integrations. The Software Developer is required to provide weekly status reports to their supervisor in the Information Technology (IT) department.\n\nEssential Position Functions\n Develop, test, document, and implement high quality learning system applications on time that perform according to specifications and requirements documents\n Analyze performance of programs and takes action to correct deficiencies\n Develop and adhere to development standards that allow for the maintainability of code in a manner that supports team development\n Participate in the modification of software applications or architecture to resolve issues and plan for future growth\n Participate in code reviews and meetings as necessary\n Provide a minimum of weekly status report updates for current projects\n Develop and manage effective working relationships with other departments, groups and personnel with whom work must be coordinated or interfaced\n Meet professional obligations through efficient work habits such as, meeting deadlines, honoring schedules, coordinating resources and meetings in an effective and timely manner, and demonstrate respect for others\n\nV2Soft is an Equal Opportunity Employer ( EOE).\n\nhttps://www.v2soft.com/careers - to view all of our open opportunities and to learn more about our benefits.",
