    job_description:
      "Camping World is seeking a highly skilled and motivated Integrations Developer with expertise in C# and .NET Core to join our dynamic development team. As an Integrations Developer, you will play a crucial role in designing, developing, and maintaining business application integrations and connected software solutions that meet our business needs. You will work to integrate our internal and customer facing applications with internal and partner systems, ensuring seamless functionality, and a great user experience.\n\nWhat You'll Do:\n Design, develop, and maintain software applications using a variety of technologies.\n Develop and maintain APIs.\n Integrate third-party services.\n Document code, APIs, and technical processes to facilitate knowledge transfer and supportability.\n Troubleshoot and debug code independently.\n Work with a team of developers to deliver high-quality products.\n\nWhat You'll Need to Have for the Role:\n Bachelor's degree in computer science or a related field\n 5+ years of experience in full stack development using C# and .NET Core.\n Strong SQL skills and experience with databases such as PostgreSQL and SQL Server\n Angular (not JS) experience\n RESTful API development experience\n Experience with integrating third-party services.\n Experience working on microservices and Kubernetes.\n Experience with cloud computing technologies such as Azure (preferred) or AWS\n Capable of creating new and following existing software development best practices and design patterns\n Experience with Agile development methodology\n Experience with unit testing and continuous integration/continuous delivery (CI/CD) pipelines\n Experience with security best practices\n Strong understanding of HTML, CSS, and JavaScript\n Experience with version control systems such as Git or GitHub\n Excellent problem-solving and debugging skills.\n Strong communication and teamwork skills\n\nHybrid in our Lincolnshire, IL or Chicago, IL offices is strong preference.\n\nAlternatively, the position can be remote and will require travel to our Lincolnshire, IL or Chicago, IL offices approximately 25% of the time or as business needs require.\n\nGeneral Compensation Disclosure\n\nThe pay range for this role considers several factors in making compensation decisions including but not limited to skill sets; experience and training; licensure and certifications; and other business and organizational needs. At Camping World, it is not typical for an individual to be hired at or near the top of the range for their role and compensation decisions are dependent on the factors stated. A reasonable estimate of the current range is listed below.\n\nPay Range:\n$103,200.00-$154,800.00 Annual\n\nIn addition to competitive pay, we offer Paid Time Off, 401(k), an Employee Assistance Program, Good Sam Roadside Assistance, discounts, paid parental leave (if eligibility is met), Tuition Reimbursement (if eligibility is met), and on the job training opportunities. Full-time associates are offered a comprehensive benefit package including medical, dental, vision and more! Part-time associates are offered access to dental & vision coverage! For more information please visit: www.mycampingworldbenefits.com\n\nWe are an equal employment opportunity employer. The Company's policy is not to discriminate against any applicant or employee based on race, color, sex, sexual orientation, gender identity, religion, national origin, age (40 and over), disability, veteran or uniformed service-member status, genetic information, or any other basis protected by applicable federal, state, or local laws.",
