    job_description:
      "Are you a team player? Are you curious to learn? Are you interested in working in meaningful projects? Do you want to work with cutting-edge technology? Are you interested in being part of a team that is working to transform and do things differently? If so, LPL Financial is the place for you!\n\nExcited to learn more? If so, then this could be the role for you!\n\nLPL Financial (Nasdaq: LPLA) was founded on the principle that the firm should work for the advisor, and not the other way around. Today, LPL is a leader* in the markets we serve, supporting more than 18,000 financial advisors, 800 institution-based investment programs and 450 independent RIA firms nationwide. We are steadfast in our commitment to the advisor-centered model and the belief that Americans deserve access to personalized guidance from a financial advisor. At LPL, independence means that advisors have the freedom they deserve to choose the business model, services, and technology resources that allow them to run their perfect practice. And they have the freedom to manage their client relationships, because they know their clients best. Simply put, we take care of our advisors, so they can take care of their clients.\n\nJob Overview:\n\nWe are looking to hire an AVP, Technology Lead in the Risk & Regulatory Technology organization. Under the direction of the Application Development Manager, this position will help solution business requirements and technology directions to support Product Roadmaps and LPL vision. This position will involve collaboration with business partners, Architects, Quality Assurance, Release Management, and the Change Control board to plan, design, develop and implement solutions for these needs.\n\nThe candidate should be capable of designing and developing timely, high quality products, with state of the art interfaces to best solution these needs, and have the drive to ensure that the vision and success criteria are met within budget and schedule constraints.\n\nResponsibilities:\n Design and develop scalable enterprise applications with expertise in full stack development in AWS cloud or on-prem environment\n Lead teams to participate in all phases of the software engineering life cycle, including Agile ceremonies.\n Provide technical troubleshooting, solutioning, and advanced debugging support.\n Evaluate new design proposals, investigate and recommend solutions and process improvements that deliver applications with high scalability, reliability, and performance.\n Assist with conceptual design and rough order of magnitude (ROM) level of effort estimations\n Ensure the necessary platform technical documentation is maintained in an appropriate manner\n\nWhat are we looking for?\n\nWe want strong collaborators who can deliver a world-class client experience. We are looking for people who thrive in a fast-paced environment, are client-focused, team oriented, and are able to execute in a way that encourages creativity and continuous improvement.\n\nRequirements:\n Bachelor's Degree in Computer Science or equivalent\n 10+ years experience with hands-on full stack application development using C#, Angular (or similar), REST API, and relational and non-relational databases following test-driven development (TDD) approach.\n 7+ years experience with enterprise software development processes, including documenting software design by using sequence diagrams, activity diagrams, etc., and presenting in architecture board reviews.\n 7+ years experience with developing Microservices, API-first, and Event-driven design patterns.\n 5+ years of experience working with source control tools like TFS, GI\n BS or advanced degree in Computer Science, Information Technology, or related field\n\nCore Competencies:\n Excellent organizational skills\n Excellent communication skills, both oral and written\n Ability to facilitate, influence and manage change to deliver solutions\n\nPreferences:\n Hands-on knowledge of AWS Cloud technologies\n Hands-on knowledge of Confluent Kafka or similar event architecture.\n Delivered projects within an Agile software development practice\n Familiarity with API management using SwaggerHub, Kong, etc.\n Knowledge of DevSecOps methods and best practices\n\nPay Range:\n$140,500.00 - $234,100.00\n\nActual base salary varies based on factors, including but not limited to, relevant skill, prior experience, education, base salary of internal peers, demonstrated performance, and geographic location. Additionally, LPL Total Rewards package is highly competitive, designed to support your success at work, at home, and at play  such as 401K matching, health benefits, employee stock options, paid time off, volunteer time off, and more. Your recruiter will be happy to discuss all that LPL has to offer!\n\nWhy LPL?\n\nAt LPL, we believe that objective financial guidance is a fundamental need for everyone. As the nations leading independent broker-dealer, we offer an integrated platform of proprietary technology, brokerage, and investment advisor services. We provide you with a work environment that encourages your creativity and growth, a leadership team that is supportive and responsive, and the opportunity to create a career that has no limits, only amazing potential.\n\nWe are one team on one mission. We take care of our advisors, so they can take care of their clients.\n\nBecause our company is not too big and not too small, you can seize the opportunity to make a real impact. We are committed to supporting workplace equality, and we embrace the different perspectives and backgrounds of our employees. We also care for our communities, and we encourage our employees to do the same. This creates an environment in which you can do your best work.\n\nWant to hear from our employees on what its like to work at LPL? Watch this!\n\nWe take social responsibility seriously. Learn more here\n\nWant to see info on our benefits? Learn more here\n\nJoin the LPL team and help us make a difference by turning lifes aspirations into financial realities. Please log in or create an account to apply to this position. Principals only. EOE.\n\nInformation on Interviews:\n\nLPL will only communicate with a job applicant directly from an @lplfinancial.com email address and will never conduct an interview online or in a chatroom forum. During an interview, LPL will not request any form of payment from the applicant, or information regarding an applicants bank or credit card. Should you have any questions regarding the application process, please contact LPLs Human Resources Solutions Center at (855) 575-6947.",
