    job_description:
      "Job Description:\n\nThe role includes designing user interactions on websites, developing servers and databases for website functionality and coding for mobile platforms. This includes designing user interactions on web pages and developing back end website applications. It also includes configuring, coding, developing, and documenting software specifications throughout the project life cycle.\n\nDuties/Tasks and Responsibilities:\n\n Be instrumental in helping to make a successful transition to a Xamarin/C#/.NET development environment\n\n Design, develop, test and maintain mobile applications using Xamarin/C# for both iOS and Android\n\n Design and develop software using disciplined software development processes that adhere to industry standard and software best practices\n\n Design, execute, assess, and troubleshoot software programs and applications. This includes configuring, coding, developing, and documenting software specifications throughout the project life cycle.\n\n Provide support to other departments when required.\n\n Integrate applications as per written specifications\n\n Translate technical requirements and design documentation into application code and modules\n\n Perform Unit Testing to ensure the application meets the needs of the government\n\n Maintain and upgrade the software following deployment\n\n Manage the end-to-end life cycle for the production of software and applications\n\n Implement requested software features and enhancements\n\n Maintain Bug List and exercise control over software configuration\n\n Document approved software configuration Change Requests (CRs)\n\nDVS\n\n#Divergent\n\n#divergent\n\nQualifications:\n\n Must possess and maintain at least a TS\n\n Minimum eight years of software development experience\n\n Proven experience as a Full Stack Developer or similar role\n\n Experience developing desktop and mobile applications\n\n Familiarity with common stacks\n\n Front-end Technology: Knowledge of multiple front-end languages and libraries (e.g. JavaScript, HTML/ CSS, XML, jQuery)\n\n Development Languages: Knowledge of multiple back-end languages (e.g. C#, Java, Python, ASP.NET MVC) and JavaScript frameworks (e.g. Angular, React, Node.js)\n\n Database Technology: Familiarity with databases (e.g. SQLServer, Oracle, MySQL), web servers\n\n Basic Design Ability: Knowledge of UI/UX and basic prototype design\n\n In depth understanding of Stored Procedures/triggers (e.g. PL/SQL, T-SQL), Functions, and Views\n\n Experience with Web API and RESTful Services\n\n Understanding of IIS, Active Directory LDAP Authentication Process, Message Queue, Windows Services\n\n Experience and working knowledge of jQuery, JavaScript, and RazorJS\n\n Experience and working knowledge of Microsoft Visual Studio (2015 & 2017)\n\n Must be available 24/7 for system troubleshooting and support\n\n On an as needed basis or as requested, troubleshoot and support the primary and COOP sites\n\n Excellent problem-solving and analytical skills as well a high degree of self-motivation\n\n Must be able to communicate technical information to customers with varying skill levels\n\nDVS\n\n#Divergent\n\n#divergent",
