    job_description:
      "Our not-so-secret sauce. Award-winning, inclusive, Top Workplace culture doesnt happen overnight. Its a result of hard work by extraordinary people. More than 10,000 of the industrys brightest talent drive our efforts to deliver purposeful work and meaningful impact every day. Learn more about what makes us different and how you can thrive as an Applications Developer at MMA. Marsh McLennan Agency (MMA) provides business insurance, employee health & benefits, retirement, and private client insurance solutions to organizations and individuals seeking limitless possibilities. With 180 offices across North America, we combine the personalized service model of a local consultant with the global resources of the worlds leading professional services firm, Marsh McLennan (NYSE: MMC). A day in the life. As an Application Developer you will collaborate with a team of developers in the design, development, coding, testing, and debugging of applications. These applications are vital to the success of the organization and add value to our clients experience. You will develop applications while promoting MMA (Marsh & McLennan Agency) technology standards, process, and procedures of the software development life cycle. This individual will apply logical, analytical, and problem-solving skills to design and create best in class applications. Our future colleague. Wed love to meet you if your professional track record includes these skills:  Been a strong contributor to a development team.  Designs, codes, tests, and debugs new and existing applications using current coding standards and best practices.  Assist with development initiatives that create complex projects that lead to high quality products.  Works with lead developers to develop project user stories, backlog, and sprints.  Be an integral part of an innovative and passionate team working on user stories, backlog, and sprints. Completing sprints on time.  Escalates problems as appropriate.  Level 2 and Level 3 application support.  Develop reusable code to decrease time to market, create uniformity of code, increase scalability, and build more agile applications  Create and develop applications with a security first mindset.  In-Depth experience using HTML, CSS, JavaScript, JSON, Angular, C#, .NET, and other languages.  Experience with C#, .NET, Java/TypeScript languages, and other languages.  Experience with REST API design and implementation.  Experience in leveraging continuous integration and robust build/test automation, with a preference for cross platform stacks and containerization (Azure DevOps, GitHub Actions, JFrog Artifactory). These additional qualifications are a plus, but not required to apply:  Bachelor's degree in computer science, related degree, or relevant experience.  3 years of development experience We know there are excellent candidates who might not check all of these boxes. Dont be shy. If youre close, wed be very interested in meeting you. Valuable benefits. We value and respect the impact our colleagues make every day both inside and outside our organization. Weve built a culture that promotes colleague well-being through robust benefit programs and resources, encourages professional and personal development, and celebrates opportunities to pursue the projects and causes that give colleagues fulfilment outside of work. Some benefits included in this role are:  Generous time off, including personal and volunteering  Tuition reimbursement and professional development opportunities  Remote work  Charitable contribution match programs  Stock purchase opportunities To learn more about a career at MMA, check us out online: http://marshmma.com/careers or flip through our recruiting brochure: https://bit.ly/3Qpcjmw Follow us on social media to meet our colleagues and see what makes us tick:  https://www.instagram.com/lifeatmma/  https://www.facebook.com/LifeatMMA  https://twitter.com/LifeatMMA  https://www.linkedin.com/company/marsh-mclennan-agency/ The applicable base salary range for this role is $73,620-128,835. The base pay offered will be determined on factors such as experience, skills, training, location, certifications, education, and any applicable minimum wage requirements. Decisions will be determined on a case-by-case basis. In addition to the base salary, this position may be eligible for performance-based incentives. We are excited to offer a competitive total rewards package which includes health and welfare benefits, tuition assistance, 401K savings and other wellbeing programs as well as employee assistance programs. Applications will be accepted until 10/17/2024 Who you are is who we are. We embrace a culture that celebrates and promotes the many backgrounds, heritages and perspectives of our colleagues and clients. We are always seeking those with ethics, talent, and ambition who are interested in joining our client-focused teams. Marsh McLennan and its affiliates are EOE Minority/Female/Disability/Vet/Sexual Orientation/Gender Identity employers. MMAIT MMADeveloper ApplicationDevelopement Developer LI-REMOTE",
