    job_description:
      "Date Posted:\n2024-10-03\n\nCountry:\nUnited States of America\n\nLocation:\nAZ233: 2800 E. Airport Drive, Tucson 2800 East Airport Drive , Tucson, AZ, 85756 USA\n\nPosition Role Type:\nOnsite\n\nAt Raytheon, the foundation of everything we do is rooted in our values and a higher calling  to help our nation and allies defend freedoms and deter aggression. We bring the strength of more than 100 years of experience and renowned engineering expertise to meet the needs of todays mission and stay ahead of tomorrows threat. Our team solves tough, meaningful problems that create a safer, more secure world.\n\nRaytheon is currently seeing a Factory IT/ Operations Technology Developer to join our team. The technologist must have a broad knowledge of computer software, hardware, and networks. The Leader must have some background with factory process, resource planning, and procurement. S/He will be responsible for ensuring our companys computer systems and applications support Raytheon manufacturing factories mission.\n\nThe technologist will develop, oversee and maintain factory information systems. Daily tasks may include coding, installing, upgrading, and monitoring software and hardware. The Technologist will maintain the essentials such as operating systems, business applications, security tools, webservers, and PCs that support the factory.\n\nWhat You Will Do\n Make recommendations for future upgrades and changes to cyber policy.\n Troubleshoot problems reported by users, triage, and isolate issues.\n Evaluate and adjust systems performance.\n Maintain network facilities in individual machines, such as drivers and settings of test stations as well as printers.\n Maintain integrity of the server deployment, and security.\n Ensure network connectivity throughout a companys LAN/WAN infrastructure is consistent with Raytheon security practices.\n Perform network address assignment.\n Assign configuration of authentication and authorization of directory services (e.g. LDAP and/ or Active Directory).\n Administer servers, factory computers, printers, software deployment, security updates, and patching.\n Implement and support cybersecurity settings on network and stand-alone workstations.\n Program and/ or script applications as needed.\n\nQualifications You Must Have\n Typically requires a Bachelor's degree and 5 years related experience or an Advanced degree and 3 years of related experience\n Operations Technology (OT) integration and automation experience (sensors, robotics, PLCs etc.)\n OT data acquisition, evaluation, and management\n Experience in Microsoft Windows Desktops (e.g., Windows XP, 7 & 10), Windows Servers (e.g., Windows Server 2008, 2012 & 216) and /or Linux/Unix operating system administration\n Experience with python, java, java script, or C++ programming languages\n The ability to obtain and maintain a U.S. government issued security clearance is required. U.S. citizenship is required, as only U.S. citizens are eligible for a security clearance.\n\nQualifications We Prefer\n Knowledge with HP server hardware support and diagnosis\n Knowledge with HP storage hardware support and diagnosis\n Knowledge of NAS and/or SAN\n Experience with firewalls - Cisco or Forcepoint products\n NetApp/ storage knowledge is a plus\n One of the following: Security+, CISSP, CNA, MCP and/or MCSA, or similar\n Strong written and verbal communication skills\n Good customer interaction skills with users of all levels\n *\n\nExperience with group policies and setting system security controls\n Experience with Microsoft Windows Active Directory Services support\n Experience with Veritas Backup Exec/NetBackup or experience with server backup concepts\n Experience with Symantec Ghost full disk imaging and Windows Desktop Backup Technologies\n\nWhat We Offer\n Our values drive our actions, behaviors, and performance with a vision for a safer, more connected world. At RTX we value: Trust, Respect, Accountability, Collaboration, and Innovation.\n If applicable/optional - Relocation Eligibility\n\nLearn More & Apply Now!\n RTX solves some of the toughest challenges in aerospace and defense. That requires expansive thinking and bold innovation  and that, in turn, requires a culture that is diverse, equitable and inclusive.\n\nWe embrace individuality and diversity of thought to fuel opportunity for our employees, our customers, and our communities. We work toward progress, knowing that a more inclusive world is critical to our mission. Not just in this moment, but always.\n\nPlease consider the following role type definition as you apply for this role.\n Onsite: Employees who are working in Onsite roles will work primarily onsite. This includes all production and maintenance employees, as they are essential to the development\n\nThe salary range for this role is 77,000 USD - 163,000 USD. The salary range provided is a good faith estimate representative of all experience levels. RTX considers several factors when extending an offer, including but not limited to, the role, function and associated responsibilities, a candidates work experience, location, education/training, and key skills.\n\nHired applicants may be eligible for benefits, including but not limited to, medical, dental, vision, life insurance, short-term disability, long-term disability, 401(k) match, flexible spending accounts, flexible work schedules, employee assistance program, Employee Scholar Program, parental leave, paid time off, and holidays. Specific benefits are dependent upon the specific business unit as well as whether or not the position is covered by a collective-bargaining agreement.\n\nHired applicants may be eligible for annual short-term and/or long-term incentive compensation programs depending on the level of the position and whether or not it is covered by a collective-bargaining agreement. Payments under these annual programs are not guaranteed and are dependent upon a variety of factors including, but not limited to, individual performance, business unit performance, and/or the companys performance.\n\nThis role is a U.S.-based role. If the successful candidate resides in a U.S. territory, the appropriate pay structure and benefits will apply.\n\nRTX anticipates the application window closing approximately 40 days from the date the notice was posted. However, factors such as candidate flow and business necessity may require RTX to shorten or extend the application window.\n\nRTX is An Equal Opportunity/Affirmative Action Employer. All qualified applicants will receive consideration for employment without regard to race, color, religion, sex, sexual orientation, gender identity, national origin, disability or veteran status, age or any other federally protected class.\n\nPrivacy Policy and Terms:\n\nClick on this link to read the Policy and Terms",
