      "Position- Apigee developer\n\nDuration- Contract\n\nLocation- Woonsocket, RI\n\nJD\n\ndesigning and leading engineering applications and broader systems and supporting DevOps, i.e. management of the process of building software\n\nExperience application design and development experience in a cloud environment\n\nExperience with Google Cloud Platform (GCP) Apigee EDGE (SaaS platform) and Apigee X (GCP platform)Designing, build, testing, and leading the development of cloud native APIs using the Google Cloud Platform Apigee EDGE platform\n\nThe Google Cloud Platform (GCP) Apigee API Developer will work with customers, cloud partners, and other internal groups to evaluate supporting new technologies and industry specific use cases and architectures based on client needs\n\nTroubleshoot and resolve problems with pre-prod and production services Design, develop, and implement Restful APIs using Java and related technologies Utilize tools like Splunk, Swagger, Postman, and Assertible for API development and testing purposes.\n\nStrong understanding on security model, oath concepts.\n\nExperience in Prometheus and Grafana Dashboard setup.\n\nDigital : Splunk, DevOps, APIGEE\n\nRegards,\n\nPallavi Verma\n\nSr. Technical Recruiter | Syntricate Technologies Inc.\n\nDirect : (phone number removed) |\n\nEmail : | Web:\n\nWe're hiring! connect with us on and visit our\n\nMinority Business Enterprise (MBE) Certified | E-Verified Corporation | Equal Employment Opportunity (EEO) Employer\n\nThis e-mail message may contain confidential or legally privileged information and is intended only for the use of the intended recipient(s). Any unauthorized disclosure, dissemination, distribution, copying or the taking of any action in reliance on the information herein is prohibited. Please notify the sender immediately by email if you have received this email by mistake and delete this e-mail from your system. You have received this email as we have your email address shared by you or from one of our data sources or from our member(s) or subscriber(s) list. If you do not want to receive any further emails or updates, please reply and request to .",
