    job_description:
      "Web Application Developer\n\nWeb Application Developer\n\nLOCATION: W.A. Lettinga Campus - Grand Rapids, Michigan*\n **Hybrid/Remote schedule possible***\n\nDEPARTMENT: Information Technology - Web/Media\n\nTARGET PAY: $60,000 - $65,000\n\nThe Role:\n\nThis position will help maintain the University's web presence by developing dynamic Web based applications; working closely with team members and end users on web tool development/implementation; documenting all applications; and providing training/support to various groups. These responsibilities are performed in an ethical manner consistent with the University's mission, vision, and values which include diversity, equity and inclusion.\n\nYou Will:\n Develop dynamic Web based applications as needed to assist students, faculty and staff in their daily jobs and tasks.\n Develop integrations to optimize functionality across the enterprise.\n Administer and support existing web applications.\n Author SQL stored procedures (and SQL statements) to support web applications, using Microsoft SQL-Server, Oracle PL/SQL or MySql as needs dictate.\n Work closely to implement standardized testing and bug-resolution procedures for applications developed by team members.\n Work closely with end-users to develop, implement and maintain web-based tools to support specific business processes.\nYou will love this job if you are:\n Someone with a few years of experience developing web applications used in a realtime fast-paced environment.\n A collaborator and contributor to team goals, willing to support others on the team to ensure progress toward our common objectives.\n Someone who thrives in a fast-paced culture. Despite a busy task list, you're organized and productive.\nAwesome if You Have:\n A bachelor's degree in a related field or equivalent experience.\n 2+ years of web application development experience.\n SQL experience.\n Version control experience required (e.g. Git).\n 2+ years of ASP.NET (C#, MVC, EF), HTML, JavaScript experience.\nExtra Awesome If You Have:\n Experience in porting legacy applications to modern platforms.\n Experience developing in content management systems (CMS) EG. Drupal, or WordPress\nOur culture, benefits and perks Empower U with:\n Freedom to grow. We offer free or reduced tuition for staff, spouses and dependents. Nationally recognized internal training and leadership programs, and career development. Is your program not available at DU? Ask about Tuition Assistance.\n A caring culture DU values work-life balance and offers generous and flexible paid time off, 403(b) with an employer contribution plus the opportunity for an additional employer match with an employee contribution, paid volunteer time off, relocation benefits and more\n A Healthier U. Wellness program, free use of Athletic Facilities, Employee Discounts, and access to Employee Assistance Program (EAP.)\nDiversity and Inclusion:\n\nAt Davenport, we believe that engaging diversity, equity and inclusion is essential to achieving educational and institutional excellence. We invite you to join us as we band together to create an environment of equity and inclusivity that leverages diverse life experiences, ideas, backgrounds and perspectives. We respect diversity and are committed to fostering an environment of support, inclusiveness and learning, both in the workplace and throughout the application process. With that in mind, we welcome applications from candidates of all backgrounds, including people of color, people with disabilities, Veterans and the LGBTQ + community. We recognize that not all job seekers have all the preferred qualifications listed in the job ad, but we strongly recommend that you apply if you meet most of the criteria.\n\nAbout Davenport University:\n\nAt Davenport University, we know what it takes to be successful. With over 155 years of providing quality education under our belts, thousands of Davenport graduates are making an impact on the world with the knowledge and skills they learned here. Our team of faculty and staff collaborate to make this possible. Joining DU means growing and advancing your own career, while making a lasting impact on our students, colleagues and the communities in which we serve.\n\nWould you like to be part of our success? It all starts here!\n\nEEO-Davenport University is committed to equal employment opportunity regardless of a person(s) national origin, race, color, religion, age, sex, sexual orientation, disability, gender identity, transgender identity, veteran or military status, marital status, height, weight, genetic information, and any other category or classification protected by law.\n\nLink to full job description CLICK HERE\n\nTo apply, visit https://davenport.csod.com/ux/ats/careersite/15/home/requisition/2356?c=davenport\n\njeid-3a687d98a2b4c5408ecb72b3adf9e592",
