      "AVP Software Engineering & Developer\n\nWho we are looking for\nWe are looking for an Enterprise Java Developer with experience in building high-performing, scalable, enterprise-grade applications. You will be part of a software team working on maintaining and enhancing our mission-critical Fixed Income and Equity Pricing application for a high-profile client. Your role in this position will include application maintenance of the existing system as well as design and implementation of new product features. You'll also be expected to provide expertise across the entire technology stack. This position is to support IMSWest development team in Irvine\nThis role can be performed in a hybrid model, where you can balance work from home and office to match your needs and role requirements.\nWhat you will be responsible for\nAs an Enterprise Java Software Engineer you will\n\n Write well designed, testable, efficient, and maintainable code\n Maintain ownership of code changes across all phases of the development lifecycle\n Ensure designs are in compliance with specifications\n Participate in department wide initiatives outside of primary role:\n Client driven initiatives\n Support continuous improvement by investigating alternatives and technologies and presenting these for architectural review What we value\nThese skills will help you succeed in this role\n\n Strong critical thinking, problem solving, and decision making skills\n Team player and strong collaboration skills\n Willing to look at migrating current processes into new technology\n Abile to communicate with all levels of staff and client resources Education & Preferred Qualifications\n\n BS/MS degree in Computer Science, Engineering or a related subject\n Hands on experience in designing and developing applications using Java EE platforms with an emphasis in EJB 3.1\n Good understanding of common design patternsUnderstanding of Java and JEE internals (Classloading, Memory Management, Transaction management, etc.)\n Excellent knowledge of Relational Databases, SQL and ORM technologies; EclipseLink (preferred), OpenJPA, Hibernate\n Strong front-end development experience in JavaScript with an emphasis in ReactJS. Other frameworks/libraries suck as Angular will be considered.\n Comfortable working in Windows, Solaris, and Linux environments\n Unix shell script skills (ksh, csh, awk, sed, etc.): 8 or higher\n Sybase or general SQL skills: 8 or higher\n Java: 7 or higher\n Skills to understand the business logic in the code Are you the right candidate? Yes!\nWe truly believe in the power that comes from the diverse backgrounds and experiences our employees bring with them. Although each vacancy details what we are looking for, we don't necessarily need you to fulfil all of them when applying. If you like change and innovation, seek to see the bigger picture, make data driven decisions and are a good team player, you could be a great fit.\nWhy this role is important to us\nOur technology function, Global Technology Services (GTS), is vital to State Street and is the key enabler for our business to deliver data and insights to our clients. We're driving the company's digital transformation and expanding business capabilities using industry best practices and advanced technologies such as cloud, artificial intelligence and robotics process automation.\nWe offer a collaborative environment where technology skills and innovation are valued in a global organization. We're looking for top technical talent to join our team and deliver creative technology solutions that help us become an end-to-end, next-generation financial services company.\nJoin us if you want to grow your technical skills, solve real problems and make your mark on our industry.\nAbout State Street\nWhat we do. State Street is one of the largest custodian banks, asset managers and asset intelligence companies in the world. From technology to product innovation, we're making our mark on the financial services industry. For more than two centuries, we've been helping our clients safeguard and steward the investments of millions of people. We provide investment servicing, data & analytics, investment research & trading and investment management to institutional clients.\nWork, Live and Grow. We make all efforts to create a great work environment. Our benefits packages are competitive and comprehensive. Details vary by location, but you may expect generous medical care, insurance and savings plans, among other perks. You'll have access to flexible Work Programs to help you match your needs. And our wealth of development programs and educational support will help you reach your full potential.\nInclusion, Diversity and Social Responsibility. We truly believe our employees' diverse backgrounds, experiences and perspectives are a powerful contributor to creating an inclusive\n\nSalary Range:\n$110,000 - $177,500 Annual\nThe range quoted above applies to the role in the primary location specified. If the candidate would ultimately work outside of the primary location above, the applicable range could differ.\n\nJob Application Disclosure:\nIt is unlawful in Massachusetts to require or administer a lie detector test as a condition of employment or continued employment. An employer who violates this law shall be subject to criminal penalties and civil liability.\n\nState Street's Speak Up Line",
