    job_description:
      "About Lumen Lumen connects the world. We are igniting business growth by connecting people, data and applications  quickly, securely, and effortlessly. Together, we are building a culture and company from the people up  committed to teamwork, trust and transparency. People power progress. Lumens commitment to workplace inclusion and employee support shines bright. Weve made the Newsweek 2024 Greatest Workplaces for Diversity list and achieved a perfect score of 100 on the Human Rights Campaign Corporate Equality Index (CEI) for the fifth consecutive year. Plus, were the top employer in the communications and telecom industry, ranking 12th overall across all industries in The American Opportunity Index. Were looking for top-tier talent and offer the flexibility you need to thrive and deliver lasting impact. Join us as we digitally connect the world and shape the future. Legal Statement In any materials you submit, you may redact or remove age-identifying information such as age, date of birth, or dates of school attendance or graduation. You will not be penalized for redacting or removing this information. The Lumen Internship Lumen offers a fully immersive, 10-week summer internship program. Our interns demonstrate curiosity, innovation, and a passion for advancing technology. We believe that empowering our people and helping them reach their full potential is essential for the long-term success of both Lumen and our customers. Program dates: May 30  August 8, 2025. Work Location This position is fully remote/work from home. The Main Responsibilities Lumen is looking for a Software Developer Intern on our Network Engineering team. The intern will be responsible for providing support to the Vyvx development team on backlog items, UI development, service order enhancements and test automation improvements. The intern will assist the developers on installing applications for operations users and provide production and defect support. Day-to-day responsibilities include: Attending daily scrum call to provide status updates Shadowing developers to understand Vyvx infrastructure and applications Partnering with lead UI developer and tester to perform new UI development and test automation Monitoring backlog items and ensure proper time reporting Ensuring reporting and application installation requests are completed promptly What We Look For in a Candidate Program eligibility is contingent on the candidates commitment of the entire 10-week program. No exceptions will be made. Required qualifications: Enrolled at a 4-year accredited college or university, rising senior level education status at the start of the internship Preferred fields include but not limited to: Engineering, Technology or related field Graduating August 2025  May 2026 Intern must be able to work 40 hours per week during the 10-week program (May 30  August 8, 2025) Proficiency and understanding of: Both front-end and back-end development languages Test automation, creating test cases and writing scripts JavaScript, JIRA, Linux and GitHub Data base migration Preferred qualifications: Quick learner Hard worker who is attentive and ready to dive in Technical, familiar with responsibilities of a Full-Stack Developer Collaborative and adaptable Compensation Internship compensation ranges depend on each individual's level of education, geographic location, and experience/qualifications aligned to the role. Hourly Based Pay Range: Min: $26.00/hour Max: $43.00/hour Work Authorization US work authorization required for role. Individuals requiring current or future sponsorship for employment are not eligible. What to Expect Next After completion and submission of your application, you will be asked to participate in a virtual video interview. This on demand interview will be a way for Lumen to learn more about you and your experience as it aligns to the internship position. A separate email invitation will be sent to you (check your spam) within 6 hours of application. For continued consideration in the summer internship program, please ensure you complete the video interview within 5 business days of application. Application & Interview Timeline: October  First Round Interviews with top, qualified candidates November - Interview panel with work team December - All Summer 2025 offers will be extended by end of month Requisition : 335179 Background Screening If you are selected for a position, there will be a background screen, which may include checks for criminal records and/or motor vehicle reports and/or drug screening, depending on the position requirements. For more information on these checks, please refer to the Post Offer section of our FAQ page (https://jobs.lumen.com/global/en/faq) . Job-related concerns identified during the background screening may disqualify you from the new position or your current role. Background results will be evaluated on a case-by-case basis. Pursuant to the San Francisco Fair Chance Ordinance, we will consider for employment qualified applicants with arrest and conviction records. Equal Employment Opportunities We are committed to providing equal employment opportunities to all persons regardless of race, color, ancestry, citizenship, national origin, religion, veteran status, disability, genetic characteristic or information, age, gender, sexual orientation, gender identity, gender expression, marital status, family status, pregnancy, or other legally protected status (collectively, protected statuses). We do not tolerate unlawful discrimination in any employment decisions, including recruiting, hiring, compensation, promotion, benefits, discipline, termination, job assignments or training. Disclaimer The job responsibilities described above indicate the general nature and level of work performed by employees within this classification. It is not intended to include a comprehensive inventory of all duties and responsibilities for this job. Job duties and responsibilities are subject to change based on evolving business needs and conditions. Salary Range Salary Min : 26 Salary Max : 43 This information reflects the anticipated base salary range for this position based on current national data. Minimums and maximums may vary based on location. Individual pay is based on skills, experience and other relevant factors. This position is eligible for either short-term incentives or sales compensation. Director and VP positions also are eligible for long-term incentive. To learn more about our bonus structure, you can view additional information here. (https://jobs.lumen.com/global/en/compensation-information) We're able to answer any additional questions you may have as you move through the selection process. As part of our comprehensive benefits package, Lumen offers a broad range of Health, Life, Voluntary Lifestyle and other benefits and perks that enhance your physical, mental, emotional and financial wellbeing. You can learn more by clicking here. (https://centurylinkbenefits.com) Please be advised that Lumen does not require any form of payment from job applicants during the recruitment process. All legitimate job openings will be posted on our official website or communicated through official company email addresses. If you encounter any job offers that request payment in exchange for employment at Lumen, they are not for employment with us, but may relate to another company with a similar name. Note: For union-represented postings, wage rates and ranges are governed by applicable collective bargaining agreement provisions. Application Deadline 10/19/2024",
