    job_description:
      "Onward Search is a specialized staffing and talent solutions company that helps professionals find top jobs with the nations leading brands. Were looking to hire a Backend Developer - 21737 for a Global online marketplace platform, a dynamic, inclusive culture, championing innovation and global commerce while empowering individuals and creating unparalleled economic opportunities.\n\nYoull join the Backend Development team to ensure system availability, addresses incidents, and collaborate on reliability and architecture while communicating with users. This is a 1-3 month of contract opportunity. The role is fully remote.\n\nBackend Developer - 21737 Responsibilities:\n\nEnsuring availability, performance, security, and scalability of production systems.\n\nServe as primary POC for LiveOps/GCX to address service incidents and troubleshoot/triage as appropriate.\n\nWork with the core engineering team to address bugs and deploy services across different environments.\n\nCollaborate with infrastructure teams to scale for service capacity and traffic spikes of 20x normal load during T1 events.\n\nProvide recommendations for architecture and process improvements.\n\nCreate and maintain service health dashboards to monitor production systems.\n\nImplement alarms where there are gaps in service health monitoring\n\nMaintaining and updating eBay Live SOP\n\nBackend Developer - 21737 Requirements:\n\nExperience with modern programming languages, e.g. Java, Go, C++\n\nExperience in building large, reliable, scalable distributed systems\n\nExperience with building event-driven applications\n\nExperience with designing and building RESTful APIs\n\nExperience with modern DevOps principles and continuous delivery\n\nWork closely w/ colleagues and customers in different functional groups and remote offices\n\nHands on experience with GraphQL is a big plus\n\nExperience on web socket is a big plus\n\nJava experience is a must\n\nRest API/GraphQL experience is important\n\nDebugging skills as this role is more on the support side than development\n\nBasic Computer Science knowledge\n\nWill speak with buyers and sellers directly so need great communication skills\n\nUnique to live so streaming experience  Live, radio streaming and video streaming tools\n\nSpringboot experience\n\nPerks & Benefits:\n Medical, Dental, and Vision Insurance\n Life Insurance\n 401k Program\n Commuter Benefit\n eLearning\n Education Reimbursement\n Ongoing Training & Development\n * To qualify for our benefits package, you must work over 30 hours per week and the length of assignment must be a minimum of 10 weeks.\n\nTo learn more about this Backend Developer - 21737 opportunity, apply now. Our recruitment team will be in touch, guide you through the interview process, and advocate on your behalf.",
