      "Fusion Innovation's recent growth over the past four years has ranked us in the top 700 in the Inc 5000 Fastest Growing Private Companies in America for 2021. In 2022, Washington Business Journal's Best Places to Work Survey provided superior results for Fusion Innovation pertaining to our employee engagement, recognition, communications, and empowerment as compared to our peers. Why? Here at Fusion, we put people first. When you join us, you don't just join a company, you join a family. If you are ready to be part of a fun and engaging team where your innovative ideas are heard, supported, and make ever lasting mission impacts for our Nation's most sensitive programs, you have come to the right place.\n\nWe're looking for software engineers that have experience in languages like Java, Ruby and Python and are interested in working in domains like big data analytics, search and discovery, and machine learning/NLP. In selecting projects, we look for challenging, mission-focused work where our teams can make a significant difference. We keep our employees on the cutting-edge by providing: valet book service, attendance to conferences, training, advanced education, workshops, and much more.\n\nInterested in hearing more? Apply today!\n\nRequired:\n\nU.S. Citizenship\n\nBachelors Degree in Computer Science, Computer Engineering, Electrical Engineering or related field\n\nExperience with at least one enterprise language (Java, Ruby, or Python)\n\nExperience with object oriented software development\n\nExperience with working with and utilizing open source software\n\nWorks well in a team environment\n\nStrong communication skills\n\nExperience with Lean Agile - Human Centered Design, Xtreme Programming, Test Driven Development, Continuous Integration & Development\n\nDesired:\n\nContinuous Integration experience (Jenkins)\n\nTest Driven Development experience\n\nDevOps Skills (Vagrant, Docker)\n\nCloud Services (AWS ecosystem)\n\nAdvanced Search Technologies (Solr, ElasticSearch, Lucene)\n\nSQL and NoSQL Datastores\n\nUI/Front-End technologies (AngularJS, D3JS)\n\nDistributed Computing\n\nVersion Control\n\nPlease note that all applicants must be U.S. citizens and require additional screening from our clients. Fusion Innovation LLC is an Equal Opportunity/Affirmative Action Employer.",
