    job_description:
      "Title\n\nApplication Developer I\n\nJob Classification\n\nDeveloper, Non-Exempt\n\nDepartment Name\n\nInstructional and Information Technology Services (IITS)\n\nSupervisor\n\nExecutive Director of Computing and Application Development\n\nWork Arrangement\n\nHybrid Eligible\n\nPosition Purpose\n\nUnder the direction of the Executive Director of Computing and Application Development, the Application Developer I supports the planning, development and implementation of IT projects. Interacts with campus-based teams and departments. Communicates effectively to convey technical information and obtain projects need to later implement and develop. Documents, tracks, and monitors problems. Develops procedures and solutions for systems. Trains staff on relevant application platforms and related components.\n\nEssential Functions\n\n45% Application Development\n Design algorithms and flowcharts to update or create new software programs and systems.\n Develop, test, and deploy departmental and campus-wide systems and applications.\n Perform problem analysis to resolve system or application issues. Perform diagnosis of problems, providing solutions within the area of responsibility, and/or through consulting with IITS team members or software vendors.\n Preserve data integrity and security based on university policies, procedures, and field best practices.\n Execute requested changes, updates, and modifications to applications and data structures when necessary.\n Maintain website databases and perform updates based on information provided. Assist with creation and integration of new databases for the institutions websites (e.g. cusm.edu, cusmhealth.org, etc.).\n\n40% Application Support\n Provide direct support to essential systems such as the student information system (SIS), learning management system (LMS), content management system (CMS), etc..\n Perform diagnosis and troubleshooting for educational systems issues, providing solutions within area of responsibility, and/or through consulting with vendors.\n Collaborate with vendors that host the various systems to ensure the successful resolution of more complex issues that occur within the systems of oversight\n Work with the marketing and communications team to implement changes to style sheets and/or functionality within the Content Management System (CMS).\n Provide guidance and training for staff, faculty, and students on how to use the systems of oversight.\n Create and maintain documentation for hosted and on-premises IT infrastructure.\n\n10% Application Training\n Receive requests for IT assistance from company employees through ticket system software. Collects detailed information to categorize requests to determine a method of resolution.\n Work directly with customers to provide services and help resolve computing services issues.\n Communicate effectively with administration, faculty, staff, and students about project updates, ticket updates, and/or issues resolutions.\n\n5% Other Duties as Assigned\n\nRequired Educational Qualifications\n Bachelors degree in computer science, information technology, or a related field. A minimum of three (3) years of experience and/or an equivalent combination of education and experience may be considered. Proficiency in one programming language and MS SQL database server is required.\n\nSkills And Abilities\n Demonstrated skill in effectively using a computer and applications like Microsoft Office (Outlook, Word, Excel, Access, and PowerPoint).\n Exceptional troubleshooting skills by using effective, investigative methods and by using help desk troubleshooting tools.\n Demonstrated skill in interpreting policy and procedures related to the position and keeping others informed.\n Demonstrated knowledge and familiarity with a wide range of Microsoft technologies such as SharePoint and Active Directory.\n Demonstrated knowledge of hosted system software and its regulations, structure, techniques, and management practices, relational database management, web development, security procedures, and disaster recovery methodologies.\n Demonstrated knowledge of both front-end and back-end programming and structure languages such as HTML/CSS, JavaScript, PHP, and SQL.\n Basic comprehension of client-server communication.\n Skill in interpreting policy and procedures related to the position and keeping others informed.\n Skill in organizing resources. Must effectively prioritize assignments to complete work in a timely and accurate manner under pressure of deadlines, and with frequent interruptions and competing priorities.\n Ability to provide guidance and counsel to users in the use of existing systems.\n Ability to work with vendors to configure systems to be consistent with institutional policies/procedures and State/Federal guidelines; communicate technical and complex information both orally and in writing; influence others toward a stated objective.\n Ability to interact professionally with diverse groups.\n Ability to develop and deliver presentations and demonstrate excellent oral and written communication skills to communicate and interact effectively with colleagues, faculty, and students.\n Ability to work on multiple tasks simultaneously and within tight timelines.\n Excellent communication skills, including written and verbal acuity.\n Excellent customer service skills.\n Remain current with developments in all aspects of technology including, but not limited to, software development, systems, application support, and artificial intelligence. This includes attending conferences, workshops, online courses, reading publications, and/or participating in online communities.\n\nPreferred Qualifications\n Bachelors degree.\n Microsoft Credentials in Azure, .NET, or SQL Server.\n Familiarity with higher education-based systems such as a Student Information System (SIS), Content Management System (CMS), and Learning Management System (LMS).\n Experience in medical education.\n Familiarity with the Family Educational Rights and Privacy Act (FERPA) (20 U.S.C.\n 1232g; 34 CFR Part 99). This law applies to all schools that receive funds under an applicable program of the U.S. Department of Education.\n Familiarity with the Health Insurance Portability and Accountability Act (HIPAA). This law aims to protect sensitive patient health information from being disclosed without the patients consent or knowledge.\n\nOther Requirements\n\nItems Used\n\nStandard office equipment including computer, printer/copy machine, and telephone.\n\nPhysical Requirements\n Sit: Constantly\n Stand: Frequently\n Walk: Frequently\n Bend: Sometimes\n Squat: Sometimes\n Climb: N/A\n Crawl: N/A\n\nMental Requirements\n Read/Comprehend: Constantly\n Write: Constantly\n Perform Calculations: Frequently\n Communicate Orally: Constantly\n Reason and Analyze: Constantly\n\nEnvironmental Requirements\n Is exposed to excessive noise: No\n Is around moving machinery: No\n Is exposed to marked changes in temperature and/or humidity: No\n Drives motorized equipment: No\n Works in confined quarters: No\n Dust: No\n Fumes: No\n\nDisclaimer\n\nThe above information has been designed to indicate the general nature and level of work performed by employees within this classification. It is not designed to contain or be interpreted as a comprehensive inventory of all duties, responsibilities, and qualifications required of employees assigned to this job.\n\nProof of full vaccination and one booster of SARS-CoV-2 (COVID-19) is a condition of employment or, if applicable, submit a request for Exception (based on Medical Exemption, Disability, and/or Religious Objection.)\n\nFLSA Status: Non-Exempt\n\nJob Description Preparation Date: August 22, 2024",
