    job_description:
      "Job Description Oracle Digital Assistant (ODA) delivers a complete AI platform to create conversational experiences for business applications through text, chat, and voice interfaces. ODA is a platform for creating conversational interfaces or chatbots. A chatbot is a computer program that simulates and processes human conversation (written or spoken), allowing humans to interact with applications and data as if they were communicating with a real person. The world is seeing a global shift toward AI in the healthcare industry and ODA is delivering AI-driven solutions in the Oracle Health & Artificial Intelligence (OHAI) space. Part of this stems from the healthcare industrys transition towards a cloud environment for data management; with the cloud, data is now available on a real-time scale for further analysis. But rather than rely on staff to meticulously comb through data, artificial intelligence enables a much efficientand in many cases, much more accurateprocess. OHAI and ODA core values are how we deliver excellence. We strive for equity, inclusion, and respect for all. We are committed to the greater good in our products and our actions. We are constantly learning and taking opportunities to grow our careers and ourselves. We challenge each other to stretch beyond our past to build our future. As a Frontend Engineer, you will work with teams of software engineers responsible for the software design, development, and operations for our new and existing products. You should be able to translate UX designs into a workable implementation, be hands-on, and be able to dive deep into any part of the UI stack. You should value simplicity and impact, work comfortably in a collaborative, agile environment, and be excited to learn. Basic Qualifications BS degree in Computer Science or relevant technical field involving coding or equivalent practical experience 4 years of total experience in software development Demonstrated ability to write maintainable, tested software code in Javascript and HTML Experience with UI frameworks, such as React/Preact, Knockout.js, Angular, Oracle JET, Backbone.js , etc Experience with UI test frameworks and test runners, such as Selenium, Mocha, Karma, etc Experience writing UI build scripts, such a Grunt, Webpack, RequireJS, etc Experience working with Node.js Proven ability to deliver products and experience with the full software development lifecycle Systematic problem-solving approach, strong communication skills, a sense of ownership, and drive Public Cloud SaaS, PaaS Services and/or related technology experience, Cloud Engineering Infrastructure Development Preferred Qualifications Experience delivering product features Experience iterating over UX designs and product requirements Able to effectively communicate technical ideas verbally and in writing (technical proposals, design specs) Experience raising the technical bar for the team Familiarity with host virtualization technologies (Containers, Docker, etc.) Experience working on large-scale, highly distributed systems Career Level - IC3 Responsibilities Our Frontend Engineers are people who can work independently and provide technical leadership to the broader organization. You should have experience developing scalable UI designs, and an understanding of how to make these interfaces fault tolerant and intuitive. The ideal candidate will be technically strong and productive; someone who knows how to balance speed and quality with iterative and incremental improvements. You understand operational excellence and know-how to reinforce a culture of being proactive within your team. You recommend and justify major changes to new and existing products and establish consensus with data-driven approaches. Disclaimer: Certain US customer or client-facing roles may be required to comply with applicable requirements, such as immunization and occupational health mandates. Range and benefit information provided in this posting are specific to the stated locations only US: Hiring Range: from $74,800 to $178,200 per annum. May be eligible for bonus and equity. Oracle maintains broad salary ranges for its roles in order to account for variations in knowledge, skills, experience, market conditions and locations, as well as reflect Oracles differing products, industries and lines of business. Candidates are typically placed into the range based on the preceding factors as well as internal peer equity. Oracle US offers a comprehensive benefits package which includes the following: Medical, dental, and vision insurance, including expert medical opinion Short term disability and long term disability Life insurance and AD&D Supplemental life insurance (Employee/Spouse/Child) Health care and dependent care Flexible Spending Accounts Pre-tax commuter and parking benefits 401(k) Savings and Investment Plan with company match Paid time off: Flexible Vacation is provided to all eligible employees assigned to a salaried (non-overtime eligible) position. Accrued Vacation is provided to all other employees eligible for vacation benefits. For employees working at least 35 hours per week, the vacation accrual rate is 13 days annually for the first three years of employment and 18 days annually for subsequent years of employment. Vacation accrual is prorated for employees working between 20 and 34 hours per week. Employees working fewer than 20 hours per week are not eligible for vacation. 11 paid holidays Paid sick leave: 72 hours of paid sick leave upon date of hire. Refreshes each calendar year. Unused balance will carry over each year up to a maximum cap of 112 hours. Paid parental leave Adoption assistance Employee Stock Purchase Plan Financial planning and group legal Voluntary benefits including auto, homeowner and pet insurance The role will generally accept applications for at least three calendar days from the posting date or as long as the job remains posted. About Us As a world leader in cloud solutions, Oracle uses tomorrows technology to tackle todays problems. True innovation starts with diverse perspectives and various abilities and backgrounds. When everyones voice is heard, were inspired to go beyond whats been done before. Its why were committed to expanding our inclusive workforce that promotes diverse insights and perspectives. Weve partnered with industry-leaders in almost every sectorand continue to thrive after 40 years of change by operating with integrity. Oracle careers open the door to global opportunities where work-life balance flourishes. We offer a highly competitive suite of employee benefits designed on the principles of parity and consistency. We put our people first with flexible medical, life insurance and retirement options. We also encourage employees to give back to their communities through our volunteer programs. Were committed to including people with disabilities at all stages of the employment process. If you require accessibility assistance or accommodation for a disability at any point, let us know by calling 1 888 404 2494, option one. Disclaimer: Oracle is an Equal Employment Opportunity Employer. All qualified applicants will receive consideration for employment without regard to race, color, religion, sex, national origin, sexual orientation, gender identity, disability and protected veterans status, or any other characteristic protected by law. Oracle will consider for employment qualified applicants with arrest and conviction records pursuant to applicable law. Which includes being a United States Affirmative Action Employer",
