    job_description:
      "Overview\n\nDecisionPoint prides itself on delivering efficient, effective solutions across a wide range of industries. Come join our team as a Software developer in support of the UNITED STATES TRANSPORTATION COMMAND MILITARY SURFACE DEPLOYMENT AND DISTRIBUTION COMMAND (SDDC).\n\nThe software developer will work with other developers, testers, business analysts, and other team members to deliver software enhancements using multiple programming languages used for web applications. Design and develop new software products or major enhancements to existing software.\n\nThis position is hybrid and will require some onsite work at Scott AFB.\n\nDuties & Responsibilities\n Develop clean, secure, and testable code\n Develop from requirements and create high-level designs\n Identify, assess, and present options for meeting the functional and technical requirements\n Utilize Agile Scrum methodology.\n Test, debug, and refine software to produce the required product\n Debug and assist the team with issues that arise across the entire development lifecycle\n Develop solutions, plans of action, and deliver capabilities based on Government requirements\n Architect solutions and create plans for modernizing applications\n Collaborate with team using tools such as VersionOne, Jira, and Confluence\n\nQualifications\n Bachelors degree and 4 years of experience or 8 years experience in related field\n Active Secret clearance\n DOD 8570-01M certification: IAT Level II or higher (e.g., Security+ CE)\n Strong communication skills and self-starter who can thrive in a distributed (remote) work environment\n Software experience in some of the following: Java, Spring, JavaScript, TypeScript, NoSQL, Docker, SOAP\n Proven abilities in managing time and workloads to meet deadlines\n Demonstrated experience using developer tools IntelliJ, NPM, Gradle, Git, and SoapUI\n Knowledge of OOP and OOD (Javascript, Java or C#)\n Experience with end-to-end system development\n Experience with C#, .Net, and other computing languages\n Experience with XML and web services\n\nDesired Skills:\n Understanding of Cloud and DevOps architecture and technologies\n Experience with DevOps software such as GitLab, Artifactory, and Jenkins\n A familiarity with DevSecOps pipeline tools such as Terraform, Ansible, Helm, AWS EKS\n Collaborate on creation and development of modernized applications\n Worked with Microsoft Visual Studio\n AWS Cloud knowledge experience\n Knowledge of Agile Principles and the application of SAFe\n Agile and SAFe development experience\n Knowledge of database technology (SQL Server, Oracle), including stored procedures and indexes\n Knowledge of software development methodologies (Scrum, Lean, Waterfall)\n\nOur Equal Employment Opportunity Policy\n EEO and Affirmative Action Policy: DecisionPoint Corporation is an Equal Employment Opportunity and Affirmative Action employer. It is the policy of DecisionPoint Corporation to provide equal employment opportunity in accordance with all applicable Equal Employment Opportunity/Affirmative Action laws, directives and regulations to all employees and qualified applicants without regard to race, ethnicity, color, religion, national origin, sex, age, disability status, pregnancy, sexual orientation, gender identity, genetic information, protected veteran status, or any other protected status under Federal, State or Local laws.\n Pay Transparency Policy: In accordance with Presidential Executive Order 13665, DecisionPoint Corporation will not discharge or in any other manner discriminate against employees or applicants because they have inquired about, discussed, or disclosed their own pay or the pay of another employee or applicant. However, employees who have access to the compensation information of other employees or applicants as a part of their essential job functions cannot disclose the pay of other employees or applicants to individuals who do not otherwise have access to compensation information, unless the disclosure is (a) in response to a formal complaint or charge, (b) in furtherance of an investigation, proceeding, hearing, or action, including an investigation conducted by the employer, or (c) consistent with the contractor's legal duty to furnish information.\n Authorization to Share Resume and Personal Information: By expressing your interest and submitting your resume for this position, you authorize DecisionPoint Corporation to share your resume, as well as personal information included on the resume, with its subsidiaries, affiliates and teaming partners for the purpose of considering you for this position and other available positions requiring comparable skills, education and experience. Should DecisionPoint Corporation. or its affiliates and teaming partners wish to initiate pre-employment discussions, you will be asked to complete an employment application and related employment documents.",
