    job_description:
      "Join a member-driven leader in a fast-paced, challenging and collaborative environment with the shared mission of supporting Americas network of rural electric cooperatives. Since its formation in 1969, National Rural Utilities Cooperative Finance Corporation, or CFC, located in Dulles, Virginia, has provided capital and industry-leading financial products to more than 900 not-for-profit, consumer-controlled utility systems across the United States. Our exceptional team delivers the CFC Difference that encompasses our values of service, integrity and excellence in all our interactions.\n\nCFC has a need for a Website Developer to join the Marketing & Digital Strategy team. This is a hybrid position requiring onsite work at our headquarters office in Sterling, VA, as well as optional remote workdays. The Website Developer is responsible for creating, building and maintaining corporate websites, online applications, supporting systems (such as video library, online learning and events platforms) and analytics.\n\nAs a Website Developer, you will:\n Maintain existing websites, including day-to-day operations to ensure websites are accurate and current.\n Be responsible for website content and responding to change requests.\n Utilize best practices to optimize website performance, usability, navigation and improve user experience.\n Be responsible for online digital assets and multimedia library.\n Participate in all phases of project delivery including business requirement analysis, UI/UX design, front end architecture, development, test, deployment, and support.\n Perform quality assurance checks of web pages.\n Triage, troubleshoot and overall site maintenance.\n Upgrades to include backfilling content to lower environments and user acceptance testing.\n Prepare monthly and ad hoc analytics reports for the websites, providing additional insight and context as needed.\n Work closely with UI/UX Designer and other Corporate Relations Group members on day-to-day activities, as well as the Business Technology Services group, outside contractors, and other stakeholders on intersecting projects.\n\nTo be successful, you will need:\n Bachelor of Arts in Computer Science, Engineering, Web Design, Digital Marketing, Information Technology, or related field.\n 3 years in front end web or software development.\n Prior experience working in project management and software development environments.\n Prior experience using website content management systems and web analytics software.\n Prior experience with Adobe Experience Manager is a plus.\n Electric cooperative, finance or energy industry experience is a plus.\n Masters degree in related field is a plus.\n Ability to develop websites using HTML, JavaScript, and CSS.\n Strong knowledge and experience with industry standard web front end frameworks/libraries such as Bootstrap, Angular and ReactJS.\n An understanding of responsive UI, layout, cross-browser compatibility, accessibility and general web functions and standards.\n Ability to multi-task and work collaboratively on cross-functional teams, and to perform with minimal direction with tight deadlines.\n Strong attention to detail and organization.\n Intermediate knowledge of both PC and Macintosh operating systems.\n Intermediate MS Office skills.\n Ability to work with standard office equipment.\n\nHere at #CFC, we offer a comprehensive benefits package that includes hybrid work options; annual incentive opportunities; an employer-paid pension plan; 401(k); medical, dental and vision insurance; a generous leave policy; onsite gym; and moreall in a friendly, professional work environment. For additional information, please visit our website at www.nrucfc.coop. CFC is an Equal Opportunity Employer committed to workforce diversity.",
