    job_description:
      "Why choose between doing meaningful work and having a fulfilling life? At MITRE, you can have both. That's because MITRE people are committed to tackling our nation's toughest challengesand we're committed to the long-term well-being of our employees. MITRE is different from most technology companies. We are a not-for-profit corporation chartered to work for the public interest, with no commercial conflicts to influence what we do. The R&D centers we operate for the government create lasting impact in fields as diverse as cybersecurity, healthcare, aviation, defense, and enterprise transformation. We're making a difference every dayworking for a safer, healthier, and more secure nation and world. Our workplace reflects our values. We offer competitive benefits, exceptional professional development opportunities, and a culture of innovation that embraces diversity, inclusion, flexibility, collaboration, and career growth. If this sounds like the choice you want to make, then choose MITREand make a difference with us.\n\nWithin MITREs Intelligence Center the Strategy and Policy Department has the mission to enhance our sponsors policy formulation, planning, and strategy execution across the national security enterprise through all-source intelligence and resource analysis that informs decision-making to address threats in an era of strategic competition. Our vision is to guide the formulation and implementation of strategy and policy that enables sponsor transformation and addresses emerging national and global challenges by unlocking the full potential of intelligence analysis to advance the missions of our sponsors. The Department is accountable and responsible for programs with the Office of the Director of National Intelligence (ODNI), the Office of the Undersecretary of Defense for Intelligence and Security (OUSD I&S), and the Defense Intelligence Agency (DIA).\n\nThe Strategy and Policy Department is seeking a talented and motivated Full Stack Software Engineer to join our dynamic team. The ideal candidate will be responsible for designing, developing, and implementing high-quality software solutions across a variety of technology stacks. This role requires a comprehensive grasp of both front-end and back-end programming, coupled with a dedication to fostering a collaborative environment for problem-solving.\n\nOur team harnesses the power of advanced tools and innovative techniques to address complex challenges faced by the US government. You will have the opportunity to collaborate with experienced data professionals in a vibrant and dynamic setting, making a substantial contribution to resolving some of the government's most intricate issues. You should have passion for building end-to-end solutions for real-world problems and an innate curiosity and an inclination for solving complex problems with open-ended tasking.\n\nRoles and Responsibilities:\n\nIn this role, you will work with a team to develop front-end and back-end scalable software solutions supporting Intelligence Community challenges. As a member of the MITRE team you will be developing and designing web architectures, working with databases, and ensuring the responsiveness of applications, among other duties.\n\nAs a Full Stack Engineer, you will:\n Support the development and deployment of applications to address sponsor challenges, with a willingness to adapt and learn.\n Ensure the performance, quality, and responsiveness of applications.\n Translate user requirements into robust, scalable, and efficient code.\n Present your solutions in a clear and user-friendly manner that can be understood by all sponsor audiences, regardless of their technical expertise.\n\nBasic Qualifications:\n Bachelors degree in computer science or computer engineering or similar field and 5 years of experience\n Active TS/SCI clearance with a FS poly.\n Proven experience as a Full Stack Developer or similar role\n Proficiency in front-end and back-end languages (e.g. JavaScript, TypeScript, Python, Java).\n Knowledge of at least one JavaScript framework (e.g. Vue, React, Angular).\n Basic knowledge of server-side languages and database technologies.\n Basic knowledge of writing SQL statements.\n Experience with CSS frameworks (e.g. Material Bootstrap, Tailwind)\n Experience working within a Linux based environment (e.g. command line, shell scripting, etc.)\n Knowledge of code repository tools and IDEs.\n This position requires a minimum of 5 days a week on-site.\n\nPreferred Qualifications:\n 5 years of experience in a technical arena and minimum of 2 years of Full Stack Development experience.\n Minimum of two years working with front-end languages and libraries (e.g. HTML/CSS, JavaScript, TypeScript)\n Minimum of two years working with back-end languages (e.g. C#, Java, Python) and JavaScript frameworks (e.g. React, Vue, Angular)\n Knowledge of at least one of the following - PostgreSQL, AWS managed DB services and SQLite is preferred.\n Knowledge of alternate JavaScript runtimes (e.g. Bun or Deno)\n\nThis requisition requires the candidate to have a minimum of the following clearance(s):\nTop Secret/SCI/Polygraph\n\nThis requisition requires the hired candidate to have or obtain, within one year from the date of hire, the following clearance(s):\nTop Secret/SCI/Polygraph\n\nWork Location Type:\nOnsite\n\nMITRE is proud to be an equal opportunity employer. MITRE recruits, employs, trains, compensates, and promotes regardless of age; ancestry; color; family medical or genetic information; gender identity and expression; marital, military, or veteran status; national and ethnic origin; physical or mental disability; political affiliation; pregnancy; race; religion; sex; sexual orientation; and any other protected characteristics. For further information please visit the Equal Employment Opportunity Commission website EEO is the Law Poster and Pay Transparency.\n\nMITRE intends to maintain a website that is fully accessible to all individuals. If you are unable to search or apply for jobs and would like to request a reasonable accommodation for any part of MITREs employment process, please email recruitinghelp@mitre.org.\n\nCopyright  2024, The MITRE Corporation. All rights reserved. MITRE is a registered trademark of The MITRE Corporation. Material on this site may be copied and distributed with permission only.\n\nBenefits information may be found here",
