    job_description:
      "About Us:\n\nCyncHealth is a statewide Health Information Exchange (HIE) with a purpose to achieve health care transformation through community betterment and collaboration while protecting the security and privacy of medical information. Join our team to play a key role in leading a health data utility, advancing interoperability, and improving healthcare.\n\nPosition Summary:\n\nThe Full Stack Developer will work as part of the CyncHealth engineering team to design and implement software solutions. In collaboration with the leadership, the Full Stack Developer will lead and participate in the complete software lifecycle including design, development, implementation, and operations.\n\nResponsibilities:\n Work across the full stack, building highly scalable distributed cloud solutions that enable positive user experiences and measurable business growth.\n Develop new features and infrastructure in support of rapidly emerging business and project requirements.\n Ensure application performance, uptime, and scale, and maintain high standards for code quality and application design.\n Work with agile development methodologies, adhering to best practices and pursuing continued learning opportunities.\n Design and implement software solutions at the application, package, class, and method level.\n Work with project team to ensure requirements and proposed solution meet standards.\n Recommend future application solutions.\n Work closely with UX/UI designers to implement visually appealing and user-friendly interfaces.\n Develop and implement contingency plans for technical environments.\n Promote DevOps principles.\n Perform test planning and execution, utilize test automation and software best practices.\n Model CyncHealths values and Code of Conduct.\n Other duties as assigned.\n\nExperience/Requirements:\n Bachelor's Degree in Computer Science or related field required,\n 3 years experience in application development required.\n Experience with JavaScript, TypeScript, Node.js, Python, and GO Language or equivalent technology required.\n Experience with AWS Fargate, Lambda, API Gateway, EKS, S3, SQS, SNS or equivalent technology required.\n Experience with React, Spring, SQL, REST, and FASTAPI or equivalent technology required.\n Experience with cloud computing, cloud services and microservices preferred.\n DevOps experience (CI/CD pipelines) required.\n Familiarity with Git preferred.\n Ability to solve complex problems independently\n Additional technologies that would be beneficial: DynamoDB, MongoDB\n Experience with responsive web application design (Bootstrap, Material Design)\n Experience designing, implementing, and using REST/GraphQL Web Services\n Experience translating design mockups and prototypes into working application designs\n Familiarity with scripting languages such PowerShell or Unix shell scripting\n Experience with Agile Frameworks such as Scrum or Kanban\n Ability to work independently with minimal supervision\n Meticulous attention to detail\n Strong decision-making and problem-solving skills\n Ability to maintain a high degree of confidentiality\n\nPhysical Requirements:\n Ability to work on a computer for extended periods of time.\n Ability to stand or sit for extended periods of time.\n Light work requiring exertion up to 20 pounds of force occasionally, and/or up to 10 pounds of force frequently, and/or a negligible amount of force constantly to move objects.\n Ability to work with reliable and predictable attendance.\n\nBenefits:\n Medical, Dental, & Vision\n 20 days paid time off annually + 9 paid holidays annually.\n Long & short-term disability (fully paid by CyncHealth)\n Life Insurance (fully paid by CyncHealth)\n 401(k) with 4% matching & immediate vesting\n Parental Leave\n Pet Insurance\n Other voluntary benefits\n\nDisclaimer:\n\nThe above statements are intended to describe the general nature and level of work being performed by individuals assigned to this position. The statements are not intended to be construed as an exhaustive list of all responsibilities, duties, and skills required for this position.\n\nMust be authorized to work in the United States.",
