    job_description:
      "Albiware is a cloud based platform that empowers restoration contractors to embrace innovative technology by making it simple and accessible. As a Y Combinator backed company, we are rapidly growing and looking for team members to grow with the company and follow our core values of: integrity, innovation, curiosity, social responsibility, and growth.\n\nJob Description\n\nAlbiware is looking for engineers that are seeking a collaborative, learning environment and are passionate about technology. The right candidates will be looking to be hands on with, and intregal to, the product they are creating.\n\nQualifications\nProficient in C# and object-oriented design\nKnowledge of ASP.NET MVC, ASP.NET Web API, and ORM (e.g. Entity Framework, Dapper).\nFamiliarity with Microsoft SQL Server and writing SQL queries\nExperience with Javascript development, concepts, and frameworks\nExperience using NuGet Package Manager\nBachelors Degree in Computer Science/Engineering or 4+ years of web development experience.\nOther relevant skills:\n\nExperience with Azure, AWS, or Rackspace Cloud;\nExperience with Confluence and / or JIRA.\nExperience in software\n\nExperience with Agile development methodology\nAbility to architect solutions and data models\nAdditional Information\nBenefits:\n\nGood balance of personal autonomy and team collaboration on projects\nWork product has a direct impact on the flagship product with regular (and positive!) client feedback\nHigh level of client adoption and use of flagship product and features\nWorking on a team of smart and kind humans passionate about leveraging technology to solve real world problems\nCompetitive salary\nGenerous PTO\nCompany Holidays + 2 Floating Holidays\nMedical, dental and vision insurance with a large portion subsidized by the company\nContinuing education, mentoring, and career development opportunities\n401k Plan + Matching in October 2022\nApplication Process:\n\nNo formal cover letter is needed but please include a few sentences describing why you are applying for this position specifically.\n\nAlbiware reaches out to certain candidates for quick phone calls to gather additional information  these usually last about 10 minutes and are scheduled in advance and according to your schedule. After the phone screen, candidates that are selected to proceed in the process will do a video or in person interview. Decisions can be made after second round interviews. In some instances, third round in-person interviews are held at our office in Downers Grove, IL.\n\nOur team is diverse - Albiware values and promotes diversity. We are an equal opportunity employer and consider qualified applicants without regard to race, color, religion, sex, national origin, ancestry, age, genetic information, sexual orientation, gender identity, marital or family status, veteran status, medical condition or disability.",
