    job_description:
      "Fidelity TalentSource is your destination for discovering your next temporary role at Fidelity Investments! We are currently sourcing for a BPM Developer to work in Fidelitys Workplace Investing in Westlake, TX.\n\nThe Senior BPM Developer is a hands-on technical role that involves designing and developing sophisticated Business Process Models that automate, digitize, and modernize end to end business processes.in Fidelitys operational environments. You will be a part of a group of highly skilled engineers that build these solutions using our Workflow platform - Olympus.\n\nThe WI Operations Modernization Chapter consists of a team of technologists and BPM experts, dedicated to modernizing and transforming the systems that run Fidelity Workplace Investings operations. We focus on building cloud-based applications to modernize legacy software while transforming our workflow system to a modern BPM platform and migrating the existing workflows to the new platform.\n\nThe Expertise You Have\n 4-6 years of proven programming background in Java or Groovy. Hands on experience in building webservices, RESTful API, Spring etc., Knowledge of AWS is desired\n Solid grasp of Software Engineering and Object-Oriented Design principals\n 3-5 years of proven JavaScript and building user interfaces. Exposure to form.io desired\n Knowledge or exposure to implementing BPM solutions using one of the top BPM products (preferably Camunda)\n Experience in business process improvement or business process re-engineering projects and working closely with business and operations teams\n Ability to collaborate with Business Analysts and Design leads to implement robust automation solutions\n Solid understanding of Agile methodology\n Bachelor's degree, with a focus in Computer Science or Information Technology or comparable experience\n Experience with financial services in an operations or technology role is a plus\nThe Skills You Bring\n Knowledge of system development methodology and analytical/problem solving skills\n Your collaborative working style, good interpersonal and relationship management, and analytical skills\n An ability to understand business challenges and objectives and translate those into a design\n Willingness to have analyze business problems and suggest technical solutions\n Your excellent verbal and written communication, organization, and time-management skills\n Your ability to learn and develop expertise in new tools and products\nThe Value You Deliver\n You engage with our business partners to understand workflows needs and transform those requirements into an implemented automated business process\n You facilitate process design sessions, analyze business processes and document current and future state\n You will design and build UIs using form.io to represent user tasks in a workflow, connectors, apis and other software modules to support your BPM model\n You and your squad deliver an optimized operational environment for key functions in Fidelitys Workplace Investing Business Unit\n\nCompany Overview\n\nFidelity TalentSource is the in-house temporary staffing provider for Fidelity Investments, one of the largest and most diversified global financial services firms in the industry. We welcome individuals from all backgrounds, including technology and customer service, to fill assignments across Fidelitys U.S.-based regional and investor center locations. If you would like to experience Fidelitys supportive and collaborative culture while expanding your skill set and developing your professional network, consider a role with Fidelity TalentSource. Apply today at FTSJobs.com.\n\nWe believe that the most effective way to attract, develop and retain a diverse workforce is to build an enduring culture of inclusion and belonging.\nFidelity TalentSource will reasonably accommodate applicants with disabilities who need adjustments to participate in the application or interview process. To initiate a request for an accommodation, please contact our HR team at HR@ ftsjobs.com.\nInformation about Fidelity investments\n\nAt Fidelity Investments, our customers are at the heart of everything we do. As a privately held company with a rich 75-year history, our mission has remained the same since our founding: to strengthen the financial well-being of our clients. We help people invest and plan for their future. We assist companies and non-profit organizations in delivering benefits to their employees. And we provide institutions and independent advisors with investment and technology solutions to help invest their own clients money. For information about working at Fidelity, visit FidelityCareers.com.\n\nFidelity TalentSource's working model blends the best of working offsite with maximizing time together in person to meet associate and business needs. Currently, most hybrid roles require associates to work onsite all business days of one assigned week per four-week period (beginning in September 2024, the requirement will be two full assigned weeks).\n\nFidelity Investments and Fidelity TalentSource are equal opportunity employers.",
