    job_description:
      "In a world of possibilities, pursue one with endless opportunities. Imagine Next!\n\nWhen it comes to what you want in your career, if you can imagine it, you can do it at Parsons. Imagine a career working with intelligent, diverse people sharing a common quest. Imagine a workplace where you can be yourself. Where you can thrive. Where you can find your next, right now. Weve got what youre looking for.\n\nJob Description:\n\nJob Posting\n\nParsons is looking for an amazingly talented Software Developer to join our team! In this role you will get to create software capabilities for a front end user interface\n\nWhat Required Skills You'll Bring:\n 3+ years experience software development using Javascript\n 2+ years experience in agile development\n Scrum\n Collaborative Development\n Recording, updating, decomposing, and tracking tasks in JIRA\n 2+ years code management experience\n Experience using BitBucket code hosting platform\n Utilizing GIT for source code management.\n Ability to obtain US Security Clearance\n\nWhat Desired Skills You'll Bring:\n Integrating WebAssembly modules into web applications via javascript glue\n Experience developing and testing multi-platform software, for both desktop and mobile operating systems. Target operating environments include Android, iOS, Linux, Windows, and Mac.\n Experience with DART programming language and Flutter framework by Google.\n Experience with military graphics, MIL-STD-2525C implementation\n Experience with Cursor-On-Target (COT) messaging format, as used by the Military Tactical Assault Kit (TAK)\n\nMinimum Clearance Required to Start:\n\nNot Applicable/None\n\nThis position is part of our Federal Solutions team.\n\nOur Federal Solutions segment delivers resources to our US government customers that ensure the success of missions around the globe. Our diverse, intelligent employees drive the state of the art as they provide services and solutions in the areas of defense, security, intelligence, infrastructure, and environmental. We promote a culture of excellence and close-knit teams that take pride in delivering, protecting, and sustaining our nation's most critical assets, from Earth to cyberspace. Throughout the company, our people are anticipating whats next to deliver the solutions our customers need now.\n\nSalary Range:\n\n$96,400.00 - $168,700.00\n\nWe value our employees and want our employees to take care of their overall wellbeing, which is why we offer best-in-class benefits such as medical, dental, vision, paid time off, Employee Stock Ownership Plan (ESOP), 401(k), life insurance, flexible work schedules, and holidays to fit your busy lifestyle!\n\nParsons is an equal opportunity employer committed to diversity, equity, inclusion, and accessibility in the workplace. Diversity is ingrained in who we are, how we do business, and is one of our companys core values. Parsons equally employs representation at all job levels for minority, female, disabled, protected veteran and LGBTQ+.\n\nWe truly invest and care about our employees wellbeing and provide endless growth opportunities as the sky is the limit, so aim for the stars! Imagine next and join the Parsons questAPPLY TODAY!About Us\n\nParsons is a digitally enabled solutions provider focused on the defense, security, and infrastructure markets. With nearly 75 years of experience, Parsons is uniquely qualified to deliver cyber/converged security, technology-based intellectual property, and other innovative services to federal, regional, and local government agencies, as well as to private industrial customers worldwide.\n\nParsons is an equal opportunity, drug-free employer committed to diversity in the workplace. Minority/Female/Disabled/Protected Veteran/LGBTQ+.\n\nFor more about Parsons, visit parsons.com and follow us on Facebook, Twitter, LinkedIn, and YouTube.",
