    job_description:
      "The Aerospace Corporation is the trusted partner to the nations space programs, solving the hardest problems and providing unmatched technical expertise. As the operator of a federally funded research and development center (FFRDC), we are broadly engaged across all aspects of space delivering innovative solutions that span satellite, launch, ground, and cyber systems for defense, civil and commercial customers. When you join our team, youll be part of a special collection of problem solvers, thought leaders, and innovators. Join us and take your place in space.\n\nAt Aerospace, we are committed to providing an inclusive and diverse workplace for all employees to share in our common passion and aspiration  to carry out a mission much bigger than ourselves.\n\nThe Engineering Applications Department is seeking an Unreal/ Unity Game Engine Developer for the Engineering Applications Department (EAD) within the Information Systems and Cyber Division (ISCD). This position will be based in El Segundo, CA.\n\nJob Summary\n\nISCD staff members couple the latest in information system technologies, such as elastic compute clouds, containerization, microservices, real-time operating systems, and visualization frameworks, with expertise in cyber security, software architecture, software engineering, data science, Artificial Intelligence, process improvement, and software development to deliver responsive, resilient, high-performance software intensive systems to our Intelligence Community, DoD, and civilian customers.\n\nEAD develops a wide variety of innovative tools and applications to support civil, commercial, and national security space systems. From virtual and augmented reality, to web, cloud, and desktop visualization; EAD evaluates and creates unique visual experiences that present data and analysis to key decision makers.\n\nWork Model\n\nThis position will be located 100% onsite in El Segundo, CA.\n\nWhat Youll Be Doing\n Collaborating with game engine developers and designers to produce visualization applications.\n Working with a team to build space exploration and educational mixed reality experiences.\n Participating in technology exploration and research efforts to create photorealistic game engine environments and mixed reality solutions.\n Assisting with current mixed reality and visualization demonstrations during technical outreach\n Training and mentorship will be provided.\n What Corporate Skills Youll Bring\n Strong written and oral communication skills\n Must work well in a team environment.\n Must possess strong organizational, time management and project management skills.\n Demonstrate flexibility and ability to adapt to changing organizational needs.\n Effective interpersonal skills to coordinate efforts and tasks with other internal and external organizations.\n Demonstrate behavior that is consistent with the companys values of Dedication to Mission Success, Technical Excellence, Commitment to Our People, Integrity and Objectivity\n\nWhat You Need to be Successful\n\nMinimum Requirements:\n Bachelor's Degree in Computer Science, Computer Engineering or related discipline or closely related technical disciplinefrom an accredited college/university, pursuing or conferred within last 12 months.\n Completion of one or more years of undergraduate coursework in Computer Science, Game Development, Mathematics, Engineering, or related discipline\n Minimum GPA 3.2\n A strong interest and/or coursework in Unreal/ Unity Game Engine Development\n Experience with C++ programming.\n Effectively communicate and work successfully within a team.\n Self-motivation and a willingness to take initiative.\n This position requires the ability to obtain and maintain a U.S. Government security clearance. U.S. citizenship is required to obtain a security clearance.\n Transcripts required.\n\nHow You Can Stand Out\n\nIt would be impressive if you have one or more of these:\n Masters Degree or Ph.D. in applicable STEM areas from an accredited programin technical related discipline.\n GPA 3.5 or higher\n Experience with other programming languages (python, C#, etc.)\n Completed courses and understanding of 3D math.\n Development experience with graphical user interfaces and lighting in Unreal Engine\n Experience creating CAD models and texturing.\n Knowledge in developing Augmented Reality and/or Virtual Reality applications.\n\nWe offer a competitive compensation package where youll be rewarded based on your performance and recognized for the value you bring to our business. The grade-based pay range for this job is listed below. Individual salaries within that range are determined through a wide variety of factors including but not limited to education, experience, knowledge and skills.\n\n(Min - Max)\n$85,000 - $95,000\n\nPay Basis: Annual\n\nLeadership Competencies\n\nOur leadership philosophy is simple: every employee, regardless of level and role, can demonstrate leadership. At Aerospace, our commitment is our people. To cultivate our talent and ensure that we have a strong pipeline of future leaders, we want individuals who:\n Operate Strategically\n Lead Change\n Engage with Impact\n Foster Innovation\n Deliver Results\n\nWays We Reward Our Employees\n\nDuring your interview process, our team will provide details of our industry-leading benefits.\n\nBenefits vary and are applicable based on Job Type. A few highlights include:\n Comprehensive health care and wellness plans\n Paid holidays, sick time, and vacation\n Standard and alternate work schedules, including telework options\n 401(k) Plan  Employees receive a total company-paid benefit of 8%, 10%, or 12% of eligible compensation based on years of service and matching contributions; employees are immediately eligible and vested in the plan upon hire\n Flexible spending accounts\n Variable pay program for exceptional contributions\n Relocation assistance\n Professional growth and development programs to help advance your career\n Education assistance programs\n An inclusive work environment built on teamwork, flexibility, and respect\n\nWe are all unique, from diverse backgrounds and all walks of life, yet one thing bonds all of us to each otherthe belief that we can make a difference. This core belief empowers us to do our best work at The Aerospace Corporation.\n\nEqual Opportunity Commitment\n\nThe Aerospace Corporation is an Equal Opportunity/Affirmative Action employer. We believe that a diverse workforce creates an environment in which unique ideas are developed and differing perspectives are valued, producing superior customer solutions. All qualified applicants will receive consideration for employment and will not be discriminated against on the basis of race, age, sex (including pregnancy, childbirth, and related medical conditions), sexual orientation, gender, gender identity or expression, color, religion, genetic information, marital status, ancestry, national origin, protected veteran status, physical disability, medical condition, mental disability, or disability status and any other characteristic protected by state or federal law. If youre an individual with a disability or a disabled veteran who needs assistance using our online job search and application tools or need reasonable accommodation to complete the job application process, please contact us by phone at 310.336.5432 or by email at ieo.mailbox@aero.org. You can also review Know Your Rights: Workplace Discrimination is Illegal, as well as the Pay Transparency Policy Statement.",
