      "Job Title: Full Stack Developer\nLocation: Edison,NJ\n\nJob Description:\n Are you a skilled Full Stack Developer with a passion for creating innovative web applications? We're seeking a talented individual to join our dynamic team and contribute to our exciting projects.\n\nKey Responsibilities:\n Develop and maintain robust back-end systems using Node.js, ES6, Typescript, and REST APIs.\n Design and implement visually appealing front-end interfaces using modern component-based frameworks like Vue or React.\n Build and manage efficient databases and applications.\n Collaborate with cross-functional teams to deliver high-quality solutions.\n Write clear and concise technical documentation.\n\nQualifications:\n Minimum 3 years of hands-on experience in full stack development.\n Strong proficiency in Node.js, ES6, Typescript, REST APIs, and modern front-end frameworks.\n Familiarity with multiple back-end languages (e.g., C#, Java, Python) and JavaScript frameworks (e.g., React, Angular, Node.js).\n Knowledge of front-end languages and libraries (e.g., HTML/CSS, JavaScript, XML, jQuery).\n Experience with DBMS technology (SQL Server, Oracle, MongoDB, MySQL) and caching mechanisms (Redis, Memcached, Varnish).\n Expertise in UI/UX design and client-side validations using HTML5, CSS3, AJAX, JSON, XML, XSLT, and JS frameworks.\n Proficiency in React Native and Node.js is a must.\n\nRequirements:\n 3-6 years of relevant work experience.\n Willingness to work evening or night shifts.\n Immediate availability (within 15 days).\n\nBenefits:\n Permanent work-from-home opportunity.\n Competitive compensation package.\n Fixed internet allowance.\n Flexible work schedule (4 days/5 days/9-day fortnight).\nBenefits:\n Medical, Vision, and Dental Insurance Plans\n 401k Retirement Fund\n24-12153",
