    job_description:
      "Job Description\n\nRequirements:\n Identify, analyze and develop interfaces and integration flows using Mule ESB Anypoint platform including Mule Runtime, Connectors, Design Center and API management\n Responsible for programming and software development using various programming languages and related tools and frameworks, reviewing code written by other programmers, requirement gathering, bug fixing, testing, documenting and implementation.\n Explain complex technical issues to project managers, developers, and architects in an easy to understand manner, and recommend clearly thought out solutions.\n Deep understanding of Synchronous / Asynchronous communication patterns and Problem solving abilities w.r.t. Message Sequencing, Correlation, Queuing\n Knowledge of Integration Design patterns for building middleware systems ground up using Message Routing, Content Enrichment, Message Filtering, Message Transformation, Guaranteed delivery, Message sequencing, Batch message processing, error handling and reconciliation mechanisms.\n Knowledge of Web Services Interoperability, WS-* standards and ability to suggest, criticize and formulate solutions in a multi-vendor and architecture committee meetings.\n Develop solutions by designing system specifications, standards and programming\n Troubleshoot issues and provide bug fixes\n Good knowledge of SOA architecture and fluency in web service standards such as WSSE and technologies (XML, SOAP, WSDL, XSD, UDDI, REST) is a must\n Strong understanding of relational databases; experience working with different adaptors and JMS queues required\n Implementation experience with Mule API management portal or another framework is preferred\n Experience with large implementation of framework-based J2EE solutions is preferred\n Knowledge of Security, Logging, Auditing, Policy Management and Performance Monitoring and KPI for end-to-end process execution\n Lead large to mid-size development teams in multi-phase, heterogonous work environments, with multiple work streams\n Provide status updates and reporting metrics to the engagement leadership and be accountable for on-time and on-budget delivery\n\nQualifications\n\nQualifications:\n 5+ years of experience working with the MuleSoft Anypoint platform as a Developer\n 8+ years of software development experience. Software integration and/or other ESB experience are a must.\n Experience in medium to large size implementations, managing geographically distributed teams\n Senior level Java development experience is a must\n Strong knowledge of EAI/SOA best practices, development methodologies & standards\n Has strong experience in the Application Integration Architecture, Solution Design, and Development using SOA/EAI solutions\n Experience with SOAP and REST a must\n Solid Programming skills using J2EE, Design Patterns, JMS, SOAP / REST Web Services, XSLT, CXF, Spring Framework, etc.\n Experience with Mule administration, configuration and tuning\n Demonstrated experience implementing Mule ESB architecture, rules, caching, etc.\n Experienced troubleshooting Mule ESB, including working with debuggers, flow analyzers and configuration tools.\n Have expertise in large distributed systems, legacy experience is considered an asset\n Background in agile and waterfall methodologies\n Have excellent interpersonal, verbal and written skills and the ability to interact with all level of stakeholders, support personnel, and clients\n Strong documentation, analytical and problem-solving skills\n Bachelors degree in Computer Science or any related field\n\nAdditional Information\n\nCertification: Has or seeking IAF level 1.",
