    job_description:
      "Overview\n\nAcuity Inc. is growing! We are looking to staff a Mid-Senior ServiceNow Developer supporting one of our premier federal clients.\n\nAcuity is focused on building cutting edge, functional applications for many government agencies. We are a team of doers with a bias towards action and have a proven track record of implementing innovative technology solutions to solve complex challenges. Our team believes in the importance of usability, user experience (UX), and design within our applications. We seek to break the stereotype of clunky government applications. We use modern design and project management tools such as rapid prototyping and agile development methodologies to build and customize solutions that are easy to use and effective at meeting their business objectives. We think of the applications we have developed as products and work to ship those products in months instead of years.\n\nOnly candidates residing in DC, MD or VA will be considered per this government customer.\n\nAcuity Intro\n\nAre you ready to use your expertise in the areas of IT Modernization, Data Enablement, and Hyperautomation to make a real difference? Join Acuity, Inc., a technology consulting firm that supports federal agencies. We combine industry partnerships and long-term federal experience with innovative technical leadership to support our customers critical missions.\nResponsibilities\n\nThe Mid-Senior ServiceNow Developer position requires the candidate to take the lead in identifying, refining, organizing, documenting, and implementing workflow automation for business processes. This individual will work in direct collaboration with the customer and other subject matter experts to develop and/or re-engineer processes. This position will also provide the following, but not limited to:\n Provide application support for the configuration, customization, and administration of the ServiceNow Platform and applications, including Incident Management, Problem Management, Change Management, and HR Service Delivery\n Provide application support for the configuration of platform capabilities to include Knowledge Management, Service Catalog, and Service Portal\n Deploy core functionality in an out-of-the-box ServiceNow Platform\n Generate workflows and integrations with latest ServiceNow technologies\n Generate Automated Test Framekwork (ATF) tests associated with code produced\n Work as part of an agile scrum team, supporting story point estimation, managing stories through their lifecycle, and actively participating in agile ceremonies\n Manage multiple tasks by prioritizing based on client need and ensure quality standards and work performance\n Work as part of a team on client engagements\n Quickly adapt to customer dynamic environment and adapt their processes and foster innovative ideas\n\nQualifications\n Bachelors degree from an accredited college or university, preferably in a related field\n Minimum of four (4) years progressively responsible experience in operations, business management, technology strategy, project management, analysis, and planning or policy development\n Extensive knowledge in ITIL and Service Management processes\n Demonstrated strength in problem-solving, process improvement, and change management\n Experience configuring and administering the ServiceNow Platform\n Strong interpersonal and team orientation skills in working with various levels of technical and business-oriented individuals\n Confident presentation and facilitation skills\n Active ServiceNow Certified System Administrator Certification\n\nDesired Qualifications\n Prior experience in a consulting environment, preferably within the public sector\n ServiceNow Certified Implementation Specialist - ITSM and/or Certified Application Developer\n\nClearance Requirements:\n Must have active TS/SCI with Poly\n\nAbout Acuity:\n\nAcuity, Inc. is a leading management and technology consulting firm that specializes in serving the federal government. Our innovative, collaborative and rewarding work environment has earned repeat honors from the Washington Business Journals Best Places to Work and SmartCEO Corporate Culture awards.\n\nWhy Choose Acuity?\n Innovative Excellence: Recognized by The Washington Post's \"Top Workplaces\" and a nine-time recipient of the Washington Business Journal's \"Best Places to Work,\" Acuity fosters an environment where innovation thrives and employees flourish.\n Competitive Compensation: We value our employees and show it through highly competitive compensation and benefits packages.\n Personal Growth: Your potential isn't just recognized at Acuity, it's actively cultivated. With tailored training, mentorship, and cutting-edge resources, we empower you to thrive personally and professionally.\n Recognition and Visibility: Stand out in a competitive industry with Acuity's exceptional customer feedback and robust opportunity channels.\n Collaborative Culture: At Acuity, teamwork isn't just a buzzword  it's the cornerstone of our success. Encouragement and support fuel our collaborative culture, where every voice matters.\n Diversity and Inclusion: Diversity isn't just a checkbox  it's who we are. At Acuity, we pride ourselves on recruiting and maintaining a workforce that celebrates diversity and treats every employee with dignity and respect.\n\nJoin Acuity, where your talents are valued, your growth is nurtured, and your impact is amplified. Together, let's shape the future of digital strategy and technology consulting.\n\nWe are an equal employment opportunity employer. All qualified applicants will receive consideration for employment without regard to race, color, religion, gender, national origin, disability status, protected veteran status or any other characteristic protected by law.",
