    job_description:
      "Are you driven to create rock-solid software solutions to feed mission critical business processes and decisions? Want to contribute to a growing financial services firm who values IT as a partner to the business? Keep reading\n\nAs we continue to grow and add top talent to the Baird IT family, we have an opportunity for an experienced .NET Software Developer for our downtown Milwaukee, WI headquarters or downtown Louisville, KY office.\n\nViewing our profession as a craft, our developers work collaboratively to build and deliver quality software for our clients in a continuous learning, agile/lean environment. They create, refactor, and enhance applications using the latest tools and techniques. We offer a DevOps culture with a rich developer community focused on software craftsmanship and adding value to the Baird business.\n\nThis role supports our Private Wealth Management Business Unit. We work in a hybrid environment and developers work onsite 2-3 days/week.\n\nAs a Developer you will:\n\nEngineer - do the work!\n Develop and build new solutions using a variety of technologies (full .NET stack, JavaScript frameworks, SQL, & PowerShell)\n Analyze, design, and refactor business applications to bring them up to current standards\n\nTest and Troubleshoot  focus on quality!\n Maintain integrity of high level design and architecture by participating in pairing and design/code reviews\n Identify and implement problem resolutions and perform system and risk analysis\n Participate in application reviews to help drive accurate and complete support documentation\n Focus on automated testing and continuous delivery\n\nCollaborate - build relationships!\n Work with team members and key stakeholders to develop high level project estimates and creation of test plans\n Partner with project teams to translate business and functional requirements into technical design\n\nLearn - be your best!\n Seek out and apply new technologies and skills in daily work through conferences, online training, reading, participation in organizations and user groups, etc.\n Participate in lunch and learns and other open learning opportunities to help further your craft.\n\nWhat makes this opportunity great:\n Ability to work on critical projects with a variety of teams and leaders, proving your ability to influence and make business impact through providing technology solutions\n Information technology is a core part of Bairds business strategy and plays a critical role in the growth and transformation of the firm\n Collaborative culture that values diverse backgrounds and perspectives while emphasizing teamwork and a strong sense of partnership\n Team of associates passionate about achieving great results for clients and give back to the communities where we live and work\n\nWhat we look for:\n B.S. in Computer Science, Computer Engineering, or Software Engineering is preferred, but we would love equivalent experience too.\n Passion for software development with diverse experience including new development, support and maintenance.\n Minimum of 3 years demonstrated experience with Microsoft stack working across tiers (Object-Oriented design, C#, MVC, ASP.NET, Web API, T-SQL, RESTful services, OAuth 2.0, Azure and Power BI)\n Strong back-end development skills with experience in C#\n Strong experience working with SQL Server and other database technologies.\n Experience with Power Automate, Python, Alteryx and/or Azure preferred.\n Ability to work collaboratively with internal clients, business analysts, project managers and other developers to elicit requirements and successfully implement software projects.\n Creative problem solver who is automation focused  success in reducing recurring work and automation to train users for self-service.\n Quality focused, motivated by small quick wins and process improvement.\n Comfortable working in an Agile/Lean development environment, successfully collaborating within and across teams.\n SharePoint development experience a plus.\n Willingness to participate in on-call support.\n Comfortable working in an Agile/Lean development environment, successfully collaborating within and across teams.\n Data integration experience a plus.\n\n#LI-SB1\n\nCommitment to Inclusion & Diversity\n\nBaird is committed to inclusion & diversity for our clients, our associates and the communities where we live and work. This commitment stems from our culture of integrity, genuine concern for others and respect for the individual. We view inclusion & diversity as an ongoing journey  one of shared responsibility, continuous improvement and a focus on progress. We invite you to join us as we work together to foster an environment where diversity unites rather than divides us.",
