    job_description:
      "Mobile Developer  Android\n\nOur client is a leader in the manufacturing of IoT components for mobile device security. To drive mobile innovation, theyre hiring a highly accomplished Mobile Developer to join their team. The ideal candidate will have extensive experience in developing robust Android applications, with a focus on security and seamless user experience.\n\nThis Role Offers:\n\n Competitive base salary as well as health coverage, stock options, and equity available.\n\n Excellent work-life balance with options for flexible scheduling.\n\n Work on product for customers in defense and enterprise.\n\n Large pool of financial resources and data tools.\n\n Access to upper management and high degree of influence over business processes.\n\n Very high average employee tenure because company works to support people who are excited about what they are doing. Employees are true contributors to the overall companys success.\n\n Company invested in growth, with plans to double headcount in the next year.\n\nFocus:\n\n Design, build, and optimize Android apps for seamless user experience and peak efficiency.\n\n Implement event-driven architectures to enhance app functionality and streamline interactions.\n\n Design and integrate complex UI components using Fragment and Jetpack Compose, ensuring smooth interoperability across modules.\n\n Work with networking and IoT protocols such as IP, TCP, BLE, UDP, DNS, and USB Ethernet to enable secure data transmission.\n\n Utilize multithreading and concurrency techniques to ensure applications run efficiently across multiple processors.\n\n Perform unit testing and maintain CI/CD pipelines to support continuous integration and automated deployment processes.\n\n Handle data conversions and encoding/decoding using hexadecimal and other data formats.\n\nSkill Set:\n\n 4+ years of professional experience in Android development with a proven record of releasing applications.\n\n Proficiency in Java and Kotlin, with experience using Android Studio and Git for version control.\n\n Expertise in event-driven architectures for mobile applications.\n\n Advanced understanding of networking protocols and IoT communication standards.\n\n Strong knowledge of concurrency, multithreading, and parallel processing in Android environments.\n\n Experience in setting up and maintaining testing frameworks and CI/CD pipelines.\n\n Experience with cross-platform development using Kotlin Multiplatform (preferred).\n\n Familiarity with Azure DevOps for managing CI/CD pipelines (preferred).\n\n Experience working on large, complex native Android codebases, focusing on performance optimization (preferred).\n\n Proven ability to scale applications to support large user bases (preferred).\n\n Experience with audio enhancement and digital waveform analysis is desirable.\n\nAbout Blue Signal:\n\nBlue Signal is a leading executive search firm with expertise in IoT recruiting. Our IoT recruiters unite professionals in internet of things, industrial IoT, and connected devices with innovative companies nationwide. Learn more at bit.ly/42Qql8h",
