    job_description:
      "Web Developer (Digital Hawaiian Medium Platforms Manager), PBB, position number 97395, UH Hilo, Imiloa Astronomy Center-Hawaiian Immersion Pathway, full-time permanent, to begin approximately November 2024, pending position clearance and availability of funding.\n\nDuties: Serves as the lead information technology administrator, overseeing current and new digital systems and services related to Hawaiian language archival and lexical resources. Develops a metadata schema for use by all long-form programming and short-form departments using multiple asset management systems; uses metadata to organize and manage digital assets; creates and edits metadata to describe the content of files, making it easier to search and retrieve them; and tracks metadata standards relevant to the curation of digital collection materials such as EAD, Dublin Core, METS, MODS, and PREMIS. Evaluates, interprets, and investigates emerging technologies and equipment for digital file formats and electronic records management; and keeps abreast of advances in information technology regarding electronic records issues, systems analysis, systems development concepts and data storage methods, media, and security; as well as technical requirements for digital preservation, including hardware, software, metadata schema and file formats. Manages and coordinates the analytics-reporting systems, Machine-Readable Cataloging (MARC) records, production and archives management table, tools, fixes, and upgrades to handle bugs or problems that exist in any part of the Ulukau system. Manages and coordinates all aspects of the Ulukau Hawaiian Electronic Library including administration, maintenance, monitoring, analyzing, reporting, digitizing, securing, updating and upgrading the web-based service and its many collections. Increases collection holdings and initiates collaborations with new potential partners for Ulukau and other digital platforms and projects of the Hawaii Imiloa/College. Coordinates the digital preservation, curation and storage of all current and newly accessioned physical and digital Hawaiian language archival materials and collections at the College including identifying and obtaining all necessary permissions, contracts and copyright license approval, and negotiating fee prices as necessary. Monitors the integrity of digital content to ensure that it has not decayed or become damaged; and ensures digital content remains alive, discoverable, accessible, and usable. Coordinates with staff for proper accessioning and preservation of all materials including the processing and addition of new material (text, audio and visual media) to the Ulukau Hawaiian Electronic Library site. Ensures consistency and accuracy in Hawaiian and English using a standard orthography throughout the system, with the exception of primary sources. Coordinates and maintains the Hawaii Imiloa/Colleges presence on the worldwide web and facilitates intranet/internet usage for both academic and administrative purposes; maintains UHH uniform web file structure; and ensures websites are current including updates to features for the Greenstone system and general world wide web standards. Produces digital products such as graphics, animations, imaging, audio and video displays, and other interactive media on a variety of web platforms, using programming and design knowledge; provides creative and technical expertise and acts as subject matter expert in digital product development. Solicits funding through potential grant funders or community partners and serves as liaise, including queries from the public and partners. Coordinates with all technical vendors, with other staff members and with all partners/contractors on the development of new curriculum and program experiences for the Hawaiian Immersion Pathway. Trains and supervises student personnel, including interns and volunteers, in creating and maintaining College web pages; in digital preservation strategies and asset management. Assists in raising community awareness of Hawaiian digital archival resources available via the College. Other duties as assigned.\n\nMinimum Qualifications:\n\nEducation and Professional Work Experience: Possession of a baccalaureate degree in Hawaiian Studies and three (3) year(s) of progressive responsibility as the lead database administrator, overseeing current and new digital systems and services related to Hawaiian language archival and lexical resources; or any equivalent combination of education and/or professional work experience which provides the required education, knowledge, skills and abilities as indicated.\n\nKnowledge, Skills, and Abilities: Considerable working knowledge of principles, practices and techniques in the area of legacy audio and contemporary digital audio hardware and software, familiarity with digital audio workstation and video editing programs, audio restoration techniques and software as demonstrated by the broad knowledge of the full range of pertinent standard and evolving concepts, principles and methodologies. Considerable working knowledge and understanding of applicable federal and state laws, rules, regulations and theories and systems associated with digital platforms. Demonstrated ability to resolve wide ranging complex problems through the use of creative reasoning and logic to accurately determine the cause of the problems and the resolution of the problems in an effective, innovative and timely manner. Demonstrated ability to interpret and present information and ideas clearly and accurately in writing, verbally and by preparation of reports and other materials. Demonstrated ability to establish and maintain effective working relationships with internal and external organizations, groups, team leaders and members, and individuals. Demonstrated ability to operate a personal computer, apply word processing software, and spreadsheets, presentation, database management, and web development software. For supervisory work, demonstrated ability to lead subordinates, manage work priorities and projects, and manage employee relations. Demonstrated oral and written fluency in the Hawaiian language and successful completion of four (4) years of university Hawaiian Language courses. Demonstrated ability to effectively communicate and collaborate with members of the Hawaiian speaking public, students, staff, faculty, and administrators. One-year experience with graphics programs, design of visual media, video production, publications and web design.\n\nDesirable Qualifications: Relevant master or doctoral degree. Experience raising community awareness of Hawaiian digital archival resources. Two (2) years of experience in database administration with responsibilities for metadata, digital preservation, digital materials, digital assets, online database, digitizing, and DACS (Describing Archives, A Content Standard)\n\nPay range: PBB minimum $5,250/month\n\nTo Apply: For a complete description, application instructions and to apply, please visit https://www.schooljobs.com/careers/hawaiiedu and search for #97395 or Digital Hawaiian Medium Platforms Manager. Please be sure to review the full job posting before applying. Failure to submit all required documents shall deem an application to be incomplete. Incomplete applications will not be considered. Note: If you have not applied for a position before using NEOGOV, you will need to create an account.\n\nClosing Date: October 23, 2024\n\nInquiries: Donnette Lei Kapono, lei.kapono@hawaii.edu",
