    job_description:
      "Who we are looking for\n\nThe Data, Markets, Corporate Functions IT team is currently seeking a senior technical developer to work in a fast pace, technology driven, customer service oriented group. The individual will work under minimal supervision, analyze, design, debug, develop and unit test, medium to large scale software enhancements and solutions as part of Global Treasurys IT division and technical problem domain. Will support Fixed Income and derivatives processing for vendor applications such as SUMMIT and modifications to State Streets own custom code\n\nThis role can be performed in a hybrid model, where you can balance work from home and office to match your needs and role requirements.\n\nWhat you will be responsible for\n\nAs Vice President you will\n Provide thought leadership in the design of back-end and front-end functionality, operational workflow, and business solution to complex Business problems\n Collaborate with Business Analysts, QA team and Product owners to turn moderately complex business requirements into working and sustainable software solution to business\n Contribute to written design, development and documentation of application functionality around vendor product and State Streets own custom code.\n Participate in Business Analysis, functional/technical spec. reviews, code reviews, review of automation/SQA tests to ensure a high quality end product,\n Participate in change management and deployment process and production implementation of large scale upgrades and changes\n Develop, test, debug, and implement software programs, applications and projects using Unix, C#, C++, Java, Python, J2EE technologies, Tomcat, webservices, distributed computing, shell scripting, autosys and other related software engineering languages and tools\n Provide expert level troubleshooting on large, mission critical system upgrades and client/regulatory specific implementations and cross-team/cross-product functionality\n Help conduct manual/automated unit tests and system integration testing with various downstream and upstream systems to ensure a high quality end product\n Help maintain a stable, error/incident free Production and test environments for users across diff regions NA and EMEA, ensure SLAs are upheld and various feeds to upstream and downstream systems are maintained\n Actively participate and assist in improving development and test methodologies and contribute to related usage of tools such as Github, Jira, Agile methodology and cloud development frameworks including continuous development and integration.\n\nWhat we value\n\nThese skills will help you succeed in this role\n Excellent team work, written, verbal communication and interpersonal skills\n Able to work well with peers, support teams, Business Analysts in a collaborative team environment, across various regions and time zones\n Able to contribute to complex design and solving complex Business problems in consultation with senior technical staff, office-of-Architecture and management\n Able to work on medium to large scale projects and on complex tasks with with minimal supervision and oversight\n\nEducation & Preferred Qualifications\n 10 to 12 years of progressively responsible professional software engineering experience preferably in a financial services product delivery setting with a B.S. or M.S. degree in software engineering, computer science or related field\n 5 to 7 years of experience in financial services industry, developing solutions for Portfolio Management, Trading, Compliance, Settlement, Post-Trade processing, Accounting, Pricing, Accruals, P&L calculations\n Knowledge of Fixed-income products, processing and Interest rate derivatives is strongly desirable.\n Experience working with products such as SUMMIT and implementation of its upgrade process is a plus\n Authoritative experience with Unix, Shell scripting, performance tuning both from application side as well DB side using Oracle Technologies is highly desirable\n Knowledge of cloud technologies related to AWS, Azure, experience in migration/implementation from on-prim to public cloud is a plus\n\nAdditional requirements\n\nAre you the right candidate? Yes!\n\nWe truly believe in the power that comes from the diverse backgrounds and experiences our employees bring with them. Although each vacancy details what we are looking for, we dont necessarily need you to fulfil all of them when applying. If you like change and innovation, seek to see the bigger picture, make data driven decisions and are a good team player, you could be a great fit.\n\nWhy this role is important to us\n\nOur technology function, Global Technology Services (GTS), is vital to State Street and is the key enabler for our business to deliver data and insights to our clients. Were driving the companys digital transformation and expanding business capabilities using industry best practices and advanced technologies such as cloud, artificial intelligence and robotics process automation.\n\nWe offer a collaborative environment where technology skills and innovation are valued in a global organization. Were looking for top technical talent to join our team and deliver creative technology solutions that help us become an end-to-end, next-generation financial services company.\n\nJoin us if you want to grow your technical skills, solve real problems and make your mark on our industry.\n\nAbout State Street\n\nWhat we do. State Street is one of the largest custodian banks, asset managers and asset intelligence companies in the world. From technology to product innovation, were making our mark on the financial services industry. For more than two centuries, weve been helping our clients safeguard and steward the investments of millions of people. We provide investment servicing, data & analytics, investment research & trading and investment management to institutional clients.\n\nWork, Live and Grow. We make all efforts to create a great work environment. Our benefits packages are competitive and comprehensive. Details vary by location, but you may expect generous medical care, insurance and savings plans, among other perks. Youll have access to flexible Work Programs to help you match your needs. And our wealth of development programs and educational support will help you reach your full potential.\n\nInclusion, Diversity and Social Responsibility. We truly believe our employees diverse backgrounds, experiences and perspectives are a powerful contributor to creating an inclusive environment where everyone can thrive and reach their maximum potential while adding value to both our organization and our clients. We warmly welcome candidates of diverse origin, background, ability, age, sexual orientation, gender identity and personality. Another fundamental value at State Street is active engagement with our communities around the world, both as a partner and a leader. You will have tools to help balance your professional and personal life, paid volunteer days, matching gift programs and access to employee networks that help you stay connected to what matters to you.\n\nState Street is an equal opportunity and affirmative action employer.\n\nSalary Range:\n$110,000 - $185,000 Annual\n\nThe range quoted above applies to the role in the primary location specified. If the candidate would ultimately work outside of the primary location above, the applicable range could differ.\n\nJob Application Disclosure:\n\nIt is unlawful in Massachusetts to require or administer a lie detector test as a condition of employment or continued employment. An employer who violates this law shall be subject to criminal penalties and civil liability.\n\nState Street's Speak Up Line",
