    job_description:
      "The Company Youll Join\n\nCarta is a platform that helps people manage equity, build businesses, and invest in the companies of tomorrow. Our mission is to unlock the power of equity ownership for more people in more places.\n\nCarta is trusted by more than 40,000 companies and over two million people in nearly 160 countries to manage cap tables, compensation, and valuations. Carta also supports nearly 7,000 funds and SPVs, and represents nearly $130B in assets under administration. Carta's tender offer solutions have returned $14B to shareholders in secondary transactions. Today, Cartas platform manages nearly three trillion dollars in equity globally.\n\nFor more information about our offices and culture, check out our Carta careers page.\n\nThe Problems You'll Solve\n\nAt Carta, our employees set out on a mission to unlock the power of equity ownership for more people in more places. We believe that the problems we solve today unlock the opportunities of tomorrow.\n\nWe are seeking a talented and experienced Senior Salesforce Developer to join our dynamic team. As a Senior Salesforce Developer, you will play a key role in overseeing the Salesforce platform and leading the environment strategy. You will be actively engaged in designing, developing, and implementing Salesforce solutions to meet the evolving needs of our organization. You will collaborate closely with cross-functional teams to deliver high-quality solutions that drive business success. As a Senior Salesforce Developer, youll work to:\n Design and develop customized Salesforce solutions to support business processes and requirements\n Implement and maintain Salesforce applications, including configuration, customization, and integration\n Collaborate with business stakeholders to gather requirements and translate them into technical solutions\n Participate in all phases of the software development lifecycle, including design, development, testing, code reviews ,deployment, and support\n Provide technical expertise and guidance to developers and team members\n Ensure compliance with Salesforce best practices and security standards\n Troubleshoot and resolve technical issues as they arise\n Manage the release process for Salesforce from planning and scheduling to deployment and post-release monitoring\n Collaborate with Salesforce administrators, developers, Solution architects to gather release requirements and ensure alignment with business objectives\n Define and implement the environment strategy for Salesforce releases, when multiple projects are going in parallel mitigating risk of overwriting the code\n Plan and coordinate release activities across multiple environments, including release scheduling, environment provisioning, and deployment coordination\n Define and implement release management best practices, processes, and tools to streamline the release lifecycle and improve efficiency\n Provide support for production deployments, troubleshoot deployment issues, and ensure successful delivery of releases with minimal downtime\n Stay informed about Salesforce platform updates, new features, and best practices, and assess their impact on our release management process\n Continuously evaluate and improve release management practices, tools, and processes to enhance efficiency, reliability, and scalability\nThe Team You'll Work With\n\nYoull be joining our Business Systems team. We deliver business system solutions which improve the customer experience across all Sales, Marketing, and Delivery teams. Our team partners with relevant business stakeholders on software selection, and ensures proper post production support execution. We drive delivery over all solutions, across business and technology teams, and integrate across all Carta products.\nAbout You\n Bachelor's degree in Computer Science, Engineering, or a related field\n 6+ years of experience as a Salesforce Developer, with a proven track record of successful project delivery recommended\n Strong proficiency in Salesforce development technologies, including Apex, Visualforce, Lightning Components, and Salesforce APIs\n Experience with Salesforce integrations, including REST and SOAP APIs\n Experience with Salesforce configuration, customization, and integration\n Experience with both Sales Cloud and Service Cloud\n Solid understanding of Salesforce data model, security model, and workflow rules\n Strong Proficiency in Copado deployment process\n Hands-on experience in resolving merge conflicts for both configurations and code\n Excellent problem-solving skills and attention to detail\n Ability to work independently and collaboratively in a fast-paced environment\n Excellent communication and interpersonal skills, with the ability to collaborate effectively across teams\n Salesforce certifications (e.g., Salesforce Certified Platform Developer) preferred\nSalary\n\nCartas compensation package includes a market competitive salary, equity for all full time roles, exceptional benefits, and, for applicable roles, commissions plans. Our minimum cash compensation (salary + commission if applicable) range for this role is:\n\n$175,950 - $207,000 in San Francisco, CA; Santa Clara, CA; New York, NY\n$167,150 - $196,650 in Seattle, WA\n\nFinal offers may vary from the amount listed based on geography, candidate experience and expertise, and other factors.\n\nWe are an equal opportunity employer and are committed to providing a positive interview experience for every candidate. If accommodations due to a disability or medical condition are needed, please connect with the recruiter via email. As a company, we value fairness, helpfulness, transparency, leadership and build our teams around these values. Check out our careers page to get to know us better as you think about your next step at Carta.\nImportant Security Notice for Candidates\n\nOur company has been targeted by individuals creating fake domains similar to ours to scam prospects and candidates. Please note that all official communications from us will come from an @carta.com domain. Be cautious of any requests for sensitive information or payments outside of our official channels. For more information about this type of scam, please review the guidelines provided by the Federal Trade Commission (FTC). If you encounter any suspicious activity, please report it immediately to recruiting-systems@carta.com.\nAwards and Acknowledgements\n\nCompanies and funds like Tribe and Pipe build their businesses on Carta. The company has been included on the Forbes Worlds Best Cloud Companies, Fast Company's Most Innovative list, and Inc.s Fastest-Growing Private Companies. Weve also been recognized as a 2023 Built In Best Place to Work in the U.S., a Muse VIBE Award winner in the Vacation and Time Off category and certified as a Great Place to Work.\n\nInterested in data privacy? Check out our policies on Privacy and CA Candidate Privacy.",
