    job_description:
      "A Snapshot of Your Day\n\nAs a Software Developer, you will be a crucial part of the local Software Development team supporting the Siemens Energy Gibsonton manufacturing facility. As a digital company, Siemens Energy continuously deploys and enhances IT systems and applications to support day-to-day shop operations. On a typical day, you will work alongside the development team to implement software solutions and provide support for various applications and tools!\n\nHow Youll Make an Impact\n\nThe Siemens Energy Gibsonton facility is a 200,000-square-foot modern manufacturing and engineering facility that produces blade castings for Gas Turbine engines. Our products power the world.\n\nAs a paperless shop, the role of IT is not just important, be essential to the success of our factory operations. Your expertise and contributions will make a significant impact by supporting existing applications and tools, providing technical recommendations for improvements, and implementing new features.\n\nYour contributions will also include but are not limited to:\n Translate application User Stories and Use Cases into functional applications or improvements.\n Design, build, and maintain efficient, reusable, and reliable code.\n Improve performance, quality, and responsiveness of applications and tools.\n Identify bottlenecks and bugs, and devise solutions.\n Help maintain code quality, organization, and readability.\n\nWhat You Bring\n Bachelors Degree in Computer Science or related\n 5+ years of proven experience\n Proficient in C#, with a good knowledge of Microsoft Development stack.\n Familiarity with the .NET framework.\n Strong understanding of object-oriented programming.\n Skill for writing reusable C# libraries.\n Familiarity with Microsoft SQL Server.\n Experience with Web Development and frameworks such as JavaScript, Kendo UI, and jQuery.\n Experience creating database schemas, store procedures, and writing queries to support business processes.\n Strong troubleshooting and problem-solving skillset, familiarity with data science and artificial intelligence.\n Ability to communicate effectively with colleagues of all levels and backgrounds, from Senior Management to Blue-Collar workforce.\n You are curious and motivated to learn about technologies and how they can be deployed to improve factory operations.\n Applicants must be legally authorized for employment in the United States without current or future employer-sponsored work authorization.\n\nAbout the Team\n\nAs a global company, Siemens Energy has a large corporate IT organization supporting the companys success. The spear-tip of that IT organization is the team supporting our manufacturing environment. As a valued and respected member of that local IT team, you will share responsibility with shop floor support specialists, software developers, and administrators, all working with the shared mission to provide best-in-class service to our facility.\n\nWho is Siemens Energy?\n\nAt Siemens Energy, we are more than just an energy technology company. We meet the growing energy demand across 90+ countries while ensuring our climate is protected. With more than 94,000 dedicated employees, we not only generate electricity for over 16% of the global community, but were also using our technology to help protect people and the environment.\n\nOur global team is committed to making sustainable, reliable, and affordable energy a reality by pushing the boundaries of what is possible. We uphold a 150-year legacy of innovation that encourages our search for people who will support our focus on decarbonization, new technologies, and energy transformation.\n\nCheck out this video to learn more about Siemens Energy: https://www.siemens-energy.com/employeevideo\n\nOur Commitment to Diversity\n\nLucky for us, we are not all the same. Through diversity, we generate power. We run on inclusion and our combined creative energy is fueled by over 130 nationalities. Siemens Energy celebrates character  no matter what ethnic background, gender, age, religion, identity, or disability. We energize society, all of society, and we do not discriminate based on our differences.\n\nRewards/Benefits\n Competitive base salary\n Annual performance bonus\n Competitive vacation entitlement and 4 annual Diversity Days\n Immediate eligibility to participant in our Flexible Benefits Program and our Company Defined Contribution Retirement Program\n Career growth and development opportunities\n Supportive work culture and a healthy work- life balance\n Competitive total rewards package\n Parental leave\n Contribute to our social responsibility initiatives\n Diversity and inclusivity focused\n\n#page\n\nhttps://jobs.siemens-energy.com/jobs\n\nEqual Employment Opportunity Statement\n\nSiemens Energy is an Equal Opportunity and Affirmative Action Employer encouraging diversity in the workplace. All qualified applicants will receive consideration for employment without regard to their race, color, creed, religion, national origin, citizenship status, ancestry, sex, age, physical or mental disability unrelated to ability, marital status, family responsibilities, pregnancy, genetic information, sexual orientation, gender expression, gender identity, transgender, sex stereotyping, order of protection status, protected veteran or military status, or an unfavorable discharge from military service, and other categories protected by federal, state or local\n\nlaw.",
