    job_description:
      "Job#: 1373171\n\nJob Description:\n\nThis is a front-end developer with extensive JavaScript, responsive design and WCAG 2.0 experience.\n\nThis developer position will be supporting the Development and Maintenance programs. As a software developer the responsibilities include, but are not limited to, working with the project manager, application owner, and resource manager, working on an agile development team to organize work, develop, test, and deploy code. Must have excellent communication and teamwork skills. Be self-directed, committed and a quick learner.\n\nThe resource needs to have strong front end development skills and will work with UI/UX designers in building user interfaces and responsive, ADA compliant applications.\n\nExperience programming application user interfaces using current JavaScript/Front-end frameworks building and implementing reusable UI components and front-end libraries.\n\nStrong background with React.js, Required, 5 years.\n\nExperience with HTML5, CSS Frameworks, including Bootstrap, Required, 5 Years.\n\nStrong knowledge and background with ADA, WCAG 2.0, Section 508 compliance policies, User Experience design principles and standards, Required, 5 years.\n\nExperience programming complex computer systems using current JAVA, this includes using relevant IDE tools such as Eclipse/Jboss Developer Studio., Required, 5 Years.\n\nExperience programming in projects using JPA framework., Required, 5 Years.\n\nExperience programming in projects using current Spring framework., Required, 4 Years.\n\nExperience programming with Spring Boot and Spring Data, Required, 4 Years.\n\nExperience programming with Java 8, Required, 4 Years.\n\nExperience participating as a programmer within an Agile development environment, Required, 4 Years.\n\nExperience programming with Oracle 19C, Required, 4 Years.\n\nExperience programming in SQL and/or PL/SQL., Desired, 4 Years.\n\nExperience programming in the JBOSS Enterprise environment, Desired, 4 Years\n\nExperience programming with Log4j, Junit, and Tiles, Desired, 4 Years.\n\nExperience programming with Crystal Reports and/or Business Objects Desired, 4 Years.\n\nEEO Employer\n\nApex Systems is an equal opportunity employer. We do not discriminate or allow discrimination on the basis of race, color, religion, creed, sex (including pregnancy, childbirth, breastfeeding, or related medical conditions), age, sexual orientation, gender identity, national origin, ancestry, citizenship, genetic information, registered domestic partner status, marital status, disability, status as a crime victim, protected veteran status, political affiliation, union membership, or any other characteristic protected by law. Apex will consider qualified applicants with criminal histories in a manner consistent with the requirements of applicable law. If you have visited our website in search of information on employment opportunities or to apply for a position, and you require an accommodation in using our website for a search or application, please contact our Employee Services Department at or .\n\nApex Systems is a world-class IT services company that serves thousands of clients across the globe. When you join Apex, you become part of a team that values innovation, collaboration, and continuous learning. We offer quality career resources, training, certifications, development opportunities, and a comprehensive benefits package. Our commitment to excellence is reflected in many awards, including ClearlyRated's Best of Staffing in Talent Satisfaction in the United States and Great Place to Work in the United Kingdom and Mexico.",
