    job_description:
      "Dallas, TX\n\nSiepe is a rapidly growing Dallas-based technology services firm, specializing in developing software applications for firms in the hedge fund and financial services industry. Our mission is to deliver a single source of truth from which investment managers can obtain strategic, actionable insights to drive competitive advantages and business growth.\n\nThe Development team is responsible for ensuring client success by building scalable, cutting-edge enterprise solutions in a fast-paced environment. As a Software Developer you will work closely with Senior Developers and Product Managers to build and enhance applications and components in our financial services product suite. You will gain fluency in capital market concepts and insight into the operations of investment management firms. We are a profitable and growing company that offers competitive compensation and benefits.\n\n(Please note, unfortunately at this time we cannot offer visa sponsorship or relocation assistance.)\n\nResponsibilities\n Design, develop, and test quality components, services, and interfaces using C# and the Microsoft technology stack\n Write clean, efficient, and maintainable code\n Contribute to all phases of the development lifecycle\n Follow development best practices and coding standards, including setting up and maintaining builds and designing reusable framework components\n Innovate our current processes and tools, both internally and externally as our client base expands\n Improve your knowledge of the financial services industry, especially as it relates to specific development assignments\n Some travel to local client offices may be required\n\nQualifications\n Bachelors degree in Computer Science, Engineering, MIS or related major\n 1 to 2+ years of development experience in C# or Java\n 1 to 2+ years of experience working with any RDBMS (SQL Server, Oracle, MySQL)\n Solid understanding of Object Oriented concepts\n Experience with HTML, CSS, JavaScript, and TypeScript a plus\n Experience with Source Control, CI/CD a plus\n Experience with Javascript front-end frameworks a plus (AngularJS, React, Vue)\n Experience with CSS Frameworks a plus\n Knowledge of or experience with unit testing frameworks\n US Citizen or Permanent Resident eligible to work in the US without sponsorship\n\nDesired Skills\n Financial services experience a plus (or a demonstrable interest in financial services)\n Strong analytical and problem-solving skills\n Ability to communicate effectively, work in a team environment, and interact with end users\n Ability to adapt to changing requirements and priorities\n Self-motivated, self-starter, and challenge-seeker\n\nPerks\n Comprehensive Benefits Package\n Paid Holidays & Vacation\n 401k Match\n Bonus Opportunities\n Learning & Development Program\n Referral Program\n Daily Catered Lunches\n Monthly Celebrations\n Quarterly Offsite Events\n Holiday Parties",
